import React, { useState } from "react";
import { Button, Col, Form, Row } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';

// API-Servicess
import { forgetPasswordService } from "../../service/auth.service";

const ForgotPassword = () => {
    const navigate = useNavigate();
    const [notValidated, setNotValidated] = useState(false);
    const [formData, setFormData] = useState('');
    
    /**
     * @function handleSubmit
     * @param event
     * @description This function is used to send forget-password module email the user
    */
    const handleSubmit = async (event) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            setNotValidated(true);
        }else{
            setNotValidated(false);
            const result = await forgetPasswordService({
                email: formData
            })
            if (result?.data?.success) {
                navigate(
                    "/otp-verify",{
                        state: {
                            email: formData
                        }
                    }
                )
            } else {
                Swal.fire({
                    title: 'Error!',
                    text: result?.response?.data?.message || "Something went wrong",
                    icon: 'error',
                    confirmButtonText: 'Try Again',
                    confirmButtonColor: "#242c44"
                })
            }
        }
    };

    const handleEmail = (e) => {
        setFormData(e.target.value)
    }

    return (<div className="container">
        <Row className="mb-5 main-top-padding">
            <Col md={{ span: 5, }} className="text-center mx-auto">{
                <h2 className="pageTitle">Forgot Password?</h2>
            }</Col>
        </Row>
        <Row>
            <Col md={{ span: 8, }} lg={{ span: 5, }} className="mx-auto">{
                <Form noValidate onSubmit={handleSubmit}>
                    <Form.Group className="mb-3 field-group mb-5" controlId="">
                        <Form.Label>Email address</Form.Label>
                        <Form.Control type="email" placeholder="Enter email" onBlur={handleEmail} required/>
                        {notValidated && <span className="errorText show">Enter valid email address</span>}
                    </Form.Group>
                    <Button variant="primary" className="btn-base" type="submit">
                        Reset password
                    </Button>
                </Form>
            }</Col>
        </Row>
    </div>)
}

export default ForgotPassword;
