
import axios from "axios"

// Auth-Services
import {
    removeUserSession,
} from "../utils/AuthService";

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER

/**
* @function loginService
* @param {username, password}
* @description This function is used to signin API functioning the user
*/

export const loginService = async({ username, password }) =>{
    try {
        const result = await axios.post(`${url}/auth/sign_in`, {
            username,
            password,
        })
        return result;
    }catch(err){
        return err
    } 
}

/**
* @function signUpService
* @param { username, email, password, password_confirmation }
* @description This function is used to signup API functioning the user
*/

export const signUpService = async ({ username, email, password, password_confirmation }) =>{
    try {
        const result = await axios.post(`${url}/auth/sign_up`, {
            username,
            email,
            password,
            password_confirmation
        })
        return result;
    }catch(err){
        return err
    } 
}


/**
* @function logoutService
* @param navigate
* @description This function is used to logout API functioning the user
*/

export const logoutService = (navigate) => {
    removeUserSession();
    navigate('/login');
};


/**
* @function forgetPasswordService
* @param {email}
* @description This function is used to send OTP to user-email for reset forget-password
*/

export const forgetPasswordService = async ({email}) => {
    try {
        const result = await axios.post(`${url}/auth/forgot_password`, {
            email
        })
        return result;
    }catch(err){
        return err
    } 
};

