import React, { useState } from "react";
import { Button, Col, Container, Form, Row, Modal } from 'react-bootstrap';
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import productimage from './../../assets/images/feed/oversize-drop-shirt.jpeg';
import Failedimage from './../../assets/images/payment-failed.png';

import './CheckoutModule.scss';

const Checkout = () => {
    /* Payment Modal */
    const [showModal, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    /* Payment failed Modal */
    const [failedModal, setShowfailed] = useState(false);
    const failedClose = () => setShowfailed(false);
    const failedShow = () => setShowfailed(true);

    return (
        <div className="App">
        <Header/>
            <Container>
            <div className="checkoutPage">
                <Row className="main-top-padding">
                    <Col md={{ span: 12, }} className="text-center-desktop mx-auto">
                        <h2 className="pageTitle">Checkout</h2>
                    </Col>
                </Row>
                <div className="c-cont">
                    <div className="c-cont-adres">
                        <h6 className="c-cont-adres-titile">Delivery Address</h6>
                        <span className="c-cont-adres-edit">Change </span>
                        <div className="c-cont-adres-flex">
                            <span className="c-cont-adres-flex-label">Johnny Depp</span>
                            <span className="c-cont-adres-flex-num">+60 9876543210</span>
                            <span className="c-cont-adres-flex-tag">Primary Address</span>
                        </div>
                        <p className="c-cont-adres-text">Lorong Ladang,41200, Klang, Selangor,  Malaysia 41200</p>
                    </div>
                    <Row className="justify-content-between">
                        <Col md={{ span: 12, }} lg={{ span: 5, }}>
                            <div className="c-cont-detail">
                                <h4 className="c-cont-detail-title">Product Detail</h4>
                                <div className="c-cont-detail-flex">
                                    <div className="c-cont-detail-flex-img">
                                        <img src={productimage} alt="" />
                                    </div>
                                    <div className="c-cont-detail-flex-cont">
                                        <h6 className="c-cont-detail-flex-cont-title">APPLE iPhone 13</h6>
                                        <span className="c-cont-detail-flex-cont-qty">Qnty : 1</span>
                                    </div>
                                </div>
                                <div className="c-cont-detail-opt mob-flex">
                                    <h6 className="c-cont-detail-opt-title">Variation</h6>
                                    <div className="c-cont-detail-opt-flex">
                                        <span className="c-cont-detail-opt-flex-opt">128 GB</span>
                                        <span className="c-cont-detail-opt-flex-price">$950.00</span>
                                    </div>
                                </div>
                                <div className="c-cont-detail-opt">
                                    <h6 className="c-cont-detail-opt-title">Addons</h6>
                                    <div className="c-cont-detail-opt-flex">
                                        <span className="c-cont-detail-opt-flex-opt">Charger</span>
                                        <span className="c-cont-detail-opt-flex-price">$200.00</span>
                                    </div>
                                    <div className="c-cont-detail-opt-flex">
                                        <span className="c-cont-detail-opt-flex-opt">Cover</span>
                                        <span className="c-cont-detail-opt-flex-price">$50.00</span>
                                    </div>
                                </div>
                                <div className="c-cont-detail-sub">
                                    <div className="c-cont-detail-sub-flex">
                                        <span className="c-cont-detail-sub-flex-label">Subtotal</span>
                                        <span className="c-cont-detail-sub-flex-price">$1200.00</span>
                                    </div>
                                    <div className="c-cont-detail-sub-flex">
                                        <span className="c-cont-detail-sub-flex-label">Promo Code</span>
                                        <span className="c-cont-detail-sub-flex-price promo">-$200.00</span>
                                    </div>
                                    <div className="c-cont-detail-sub-from">
                                        <Form.Control
                                        type="text"
                                        id=""
                                        aria-describedby="promocode"
                                        />
                                        <Button className="btn-base">Apply</Button>
                                    </div>
                                </div>

                                <div className="c-cont-detail-sub">
                                    <div className="c-cont-detail-sub-flex">
                                        <span className="c-cont-detail-sub-flex-label">Total Price</span>
                                        <span className="c-cont-detail-sub-flex-price">$1000.00</span>
                                    </div>
                                </div>
                            </div>
                        </Col>
                        <Col md={{ span: 12, }} lg={{ span: 6, }}>
                            <div className="c-cont-price">
                                <h4 className="c-cont-price-title">Price Details</h4>
                                <div className="c-cont-price-sub">
                                    <div className="c-cont-price-sub-flex">
                                        <span className="c-cont-price-sub-flex-label">Price (3 items)</span>
                                        <span className="c-cont-price-sub-flex-price">$1200.00</span>
                                    </div>
                                    <div className="c-cont-price-sub-flex">
                                        <span className="c-cont-price-sub-flex-label">Promo Code discount</span>
                                        <span className="c-cont-price-sub-flex-price promo">-$200.00</span>
                                    </div>
                                </div>

                                <div className="c-cont-price-sub">
                                    <div className="c-cont-price-sub-flex">
                                        <span className="c-cont-price-sub-flex-label">Total Price</span>
                                        <span className="c-cont-price-sub-flex-price">$1000.00</span>
                                    </div>
                                </div>
                            </div>
                            <div className="c-cont-method">
                                <Form.Group className="field-group" controlId="">
                                    <Form.Label className="c-cont-method-label">Payment Method</Form.Label>
                                    <div className="select-group">
                                        <Form.Select className={'form-select'}>
                                        <option>Stripe</option>
                                        <option>Stripe</option>
                                        <option>Stripe</option>
                                        <option>Stripe</option>
                                        </Form.Select>
                                    </div>
                                </Form.Group>
                            </div>
                            <Button className="btn-base mt-5" onClick={handleShow}>Place Order</Button>
                        </Col>
                    </Row>
                </div>
            </div>
        </Container>
        <Footer />
        {/* Payment Modal*/}
        <Modal show={showModal} onHide={handleClose} className="custom-modal">
            <Modal.Header closeButton className="custom-modal-header">
            <Modal.Title className="custom-modal-header-title">Payment</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="bid-modal">
                    <div className="payment-modal">
                        <h6 className="payment-modal-title">Bank Transfer in Malaysia</h6>
                        <div className="payment-modal-flex">
                            <span className="payment-modal-flex-label">Holder Name:</span>
                            <span className="payment-modal-flex-opt">Jynie</span>
                        </div>
                        <div className="payment-modal-flex">
                            <span className="payment-modal-flex-label">Account number: </span>
                            <span className="payment-modal-flex-opt">1234567890987</span>
                        </div>
                        <div className="payment-modal-flex-between">
                            <span className="payment-modal-flex-between-label">Total amount</span>
                            <span className="payment-modal-flex-between-label">$1000.00</span>
                        </div>
                    </div>
                    <div className="modal-form">
                        <Form.Group className="mb-3 field-group" controlId="formFile">
                            <Form.Label>Attach payment Image</Form.Label>
                            <div className="amount">
                                <Form.Control
                                className={'attach-photo'}
                                type={'file'}
                                required
                                />
                                <span className="errorText">Invalid Error</span>
                            </div>
                        </Form.Group>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer className="pt-3">
            <Button variant="primary" className="btn-base" onClick={failedShow}>
            Proceed
            </Button>
            </Modal.Footer>
        </Modal>

        {/* Payment Failed Modal*/}
        <Modal show={failedModal} onHide={failedClose} className="custom-modal">
            <Modal.Header closeButton className="custom-modal-header">
            <Modal.Title className="custom-modal-header-title"></Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="failed-modal">
                    <img src={Failedimage} alt="" />
                    <h4 className="failed-modal-title">Oops! your payment has failed</h4>
                    <p className="failed-modal-text">Your payment was not successfully processed, Please try again</p>
                    <Row className="justified-content-center">
                        <Col sm={{ span: 12, }} lg={{ span: 6, }}>
                            <Button className="btn-outline" onClick={failedClose}>Cancel</Button>
                        </Col>
                        <Col sm={{ span: 12, }} lg={{ span: 6, }}>
                            <Button className="btn-base" onClick={failedClose}>Try again</Button>
                        </Col>
                    </Row>
                </div>
            </Modal.Body>
            <Modal.Footer className="pt-3">
            </Modal.Footer>
        </Modal>
    </div>
    );
}

export default Checkout;
