
import axios from "axios"

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER

/**
* @function listOfPackagesService
* @param { token }
* @description This function is used to get list of packages
*/

export const listOfPackagesService = async({ token }) =>{
    try {
        const result = await axios.get(`${url}/package`, {
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}

/**
* @function buyPackageService
* @param { token }
* @description This function is used to get list of packages
*/

export const buyPackageService = async({ token, id }) =>{
    try {
        const result = await axios.post(`${url}/package`, {
            package_id: id
        },{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}