import React, {} from "react";
import { Card} from "react-bootstrap";
import { Link } from "react-router-dom";
import './Celebritiesmodule.scss';
import CelebritiesPlaceholderimage from './../../../assets/images/celebrities-img.jpg';

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER_IMAGE

const Celebrities = ({celebrityItem}) => {
    return (
        <div className="celebrity-apart">
            <Card className="c-widget">
                <div className="c-widget-image">
                    <Link to="/profile">
                        <Card.Img variant="top" src={celebrityItem?.image ? `${url}${celebrityItem?.image}` : CelebritiesPlaceholderimage} />
                    </Link>
                </div>
                <Card.Body className="c-widget-body">
                    <Card.Title className="c-widget-body-title"><Link to="/profile">{celebrityItem?.username}</Link></Card.Title>
                </Card.Body>
            </Card>
        </div>
    );
};

export default Celebrities;
