import axios from "axios"

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER

/**
* @function getUserProfileService
* @param {token}
* @description This function is used to get login user information
*/

export const getUserProfileService = async({ token }) =>{
    try {
        const result = await axios.get(`${url}/profile`,{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}



/**
* @function sellerRequestService
* @param {token}
* @description This function is used to place a request to admin for active seller account
*/

export const sellerRequestService = async({ token }) =>{
    try {
        const result = await axios.post(`${url}/profile/seller_request`,{},{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}