import React, {} from "react";
import './SearchnotFoundmodule.scss';
import Notfoundimg from "./../../assets/images/icons/not-found-image.svg";

const Seacrhnotfound = ({searchTerm}) => {
    return (
        <div className="s-notfound">
            <h6 className="s-notfound-item">You searched for {searchTerm ? searchTerm : "Noting"}</h6>
            <div className="s-notfound-image">
                <img src={Notfoundimg} alt="" />
            </div>
            <h2 className="s-notfound-title">We couldn't find any matches!</h2>
            <p className="s-notfound-text">Please check the spelling or try searching something else</p>
        </div>
    );
};

export default Seacrhnotfound;
