import React, {useState, useEffect} from "react";
import { Button, Col, Row, Form } from 'react-bootstrap';
import { useNavigate, useLocation } from 'react-router-dom';
import Swal from 'sweetalert2';

// API-Service
import { forgetPasswordValidationService, forgetPasswordResendOTPService } from "../../service/auth.service";

import verificationIcon from './../../assets/images/icons/verification-icon.svg';
import './Verification.scss';

const  Verification = () => {
        const { state } = useLocation();
        const navigate = useNavigate();
        const [notValidated, setNotValidated] = useState(false);
        const [seconds, setSeconds] = useState(30)
        const [minutes, setMinutes] = useState(0)

        useEffect(()=>{
            if(!state){
                navigate('/forget-password')
            }
        },[state])

        useEffect(()=>{
            let myInterval = setInterval(() =>{
                if (seconds > 0) {
                  setSeconds(seconds - 1)
                }else{
                    setNotValidated(false)
                }
                if (seconds === 0){
                  if(minutes === 0){
                    clearInterval(myInterval)
                  }else{
                    setMinutes(minutes - 1)
                    setSeconds(59)
                  }
                }
            }, 1000)
            return () => {
                clearInterval(myInterval)
            }
        },[seconds])

        const handleSubmit = async (event) => {
            event.preventDefault();
            const form = event.currentTarget;
            if (form.checkValidity() === false) {
                setNotValidated(true);
            }else{
                setNotValidated(false);
                const formData = new FormData(event.target),
                formDataObj = Object.fromEntries(formData.entries()),
                values = Object.values(formDataObj).join('');

                const result = await forgetPasswordValidationService({
                    email: state?.email,
                    code: values
                })
                if (result?.data?.success) {
                    navigate("/reset-password",
                    {
                        state: {
                            code: values,
                            email: state?.email
                        }
                    })
                } else {
                    Swal.fire({
                        title: 'Error!',
                        text: result?.response?.data?.message || "Something went wrong",
                        icon: 'error',
                        confirmButtonText: 'Try Again',
                        confirmButtonColor: "#242c44"
                    })
                }
            }
        };

        const handleResendOTP = async() => {
            const result = await forgetPasswordResendOTPService({
                email: state?.email
            })
            if (result?.data?.success) {
                setSeconds(30)
            } else {
                Swal.fire({
                    title: 'Error!',
                    text: result?.response?.data?.message || "Something went wrong",
                    icon: 'error',
                    confirmButtonText: 'Try Again',
                    confirmButtonColor: "#242c44"
                })
            }
        }
           

        return (
            <div className="container">
                <Row className="mt-5 pt-4 mb-5">
                    <Col md={{ span: 8, }} lg={{ span: 6, }} className="mx-auto">{
                        <div className="v-box">
                            <figure className="v-box-icon text-center">
                                <img src={verificationIcon} alt="" />
                            </figure>
                            <div className="v-box-title">
                                <h2>Authenticate Your Account</h2>
                            </div>
                            <div className="v-box-text">
                                <p>Please confirm your account by entering the verification code sent to <span>{state?.email}</span></p>
                            </div>
                            <div className="v-box-otp">
                                <Form noValidate onSubmit={handleSubmit} className="mt-5">
                                    <div className="form-wrap">
                                        <Form.Control className={notValidated ? "otp no-validate": "otp"} type="text" maxLength='1' name="value1" required />
                                        <Form.Control className={notValidated ? "otp no-validate": "otp"} type="text" maxLength='1' name="value2" required />
                                        <Form.Control className={notValidated ? "otp no-validate": "otp"} type="text" maxLength='1' name="value3" required />
                                        <Form.Control className={notValidated ? "otp no-validate": "otp"} type="text" maxLength='1' name="value4" required />
                                        <Form.Control className={notValidated ? "otp no-validate": "otp"} type="text" maxLength='1' name="value5" required />
                                        <Form.Control className={notValidated ? "otp no-validate": "otp"} type="text" maxLength='1' name="value6" required />
                                    </div>
                                    {notValidated && <span className="errorText show">Enter valid OTP code</span>}
                                    <div className="v-box-time">
                                    {seconds !== 0 ? <span>00: {seconds < 10 ? `0${seconds}` : seconds}</span> : <span>Please resend code</span>}
                                    </div>
                                    <Row className="v-box-btnArea">
                                        <Col lg="4" className="align-right"><Button onClick={handleResendOTP} className= {seconds !== 0 ? "btn-text disabled" : "btn-text"}>Resend code</Button></Col>
                                        <Col lg="4"><Button className="btn-base" type="submit">submit</Button></Col>
                                    </Row>
                                </Form>
                            </div>
                        </div>
                    }</Col>
                </Row>
            </div>
        );
    }

export default Verification;
