import React, { useState, useEffect } from "react";
import { Button, Card, CardGroup, Col, Container, ListGroup, Row } from 'react-bootstrap';
import InfiniteScroll from 'react-infinite-scroll-component';
import { Facebook as SkeletonLoader} from 'react-content-loader';
import Swal from 'sweetalert2';
import { useNavigate } from 'react-router-dom';

import logo from "../../assets/images/logo.png"
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import Newsfeed from '../../components/Newsfeed/Newsfeed';

// Auth-Services
import { removeUserSession, getToken } from "../../utils/AuthService";

// API-Services
import { newsFeedListService } from "../../service/newsFeed.service";

import './HomeNewsfeed.scss';

const HomeNewsfeed = () => {
  
    const [newsFeedList, setNewsFeedList] = useState([]);

    const [isHasmore, setIsHasmore] = useState(true);
    const [counter, setCounter] = useState(1);

    const token = getToken();
    const navigate = useNavigate();

    const handleFetchNewsFeed = async(page) => {
        const  result = await newsFeedListService({ token, perPage: 3, page});
        if (result?.data?.success) {
            if (newsFeedList?.length >= result?.data?.data?.newsfeed?.total) {
                setIsHasmore(false);
                return;
            }
            setNewsFeedList(newsFeedList?.concat(result?.data?.data?.newsfeed?.data));
            setCounter(counter+1);
        } else {
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }


    useEffect(() => {
        if (newsFeedList?.length === 0) {
            handleFetchNewsFeed(1);
        }
        const timeoutId = setTimeout(() => 3000);
        return function cleanup() {
            clearTimeout(timeoutId);
        }
    }, [])

    return (
        <div className="App">
            <Header siteLogo={logo} />
            <div className="container">
                <Row className="mb-5 main-top-padding">
                    <Col lg={{ span: 7, }} xs={{ span: 12, }} className="mx-auto reset-padding-mobile">
                        <InfiniteScroll
                            dataLength={newsFeedList.length}
                            next={() => handleFetchNewsFeed(counter)}
                            hasMore={isHasmore}
                            loader={
                                <div>
                                    <SkeletonLoader/>
                                    <SkeletonLoader/>
                                </div>
                            }
                            endMessage={
                                <p style={{ textAlign: 'center' }}>
                                    <b>Yay! You have seen it all</b>
                                </p>
                            }
                        >
                            {newsFeedList?.map((feedItem) => <Newsfeed key={feedItem.id} feedItem={feedItem} /> )}
                        </InfiniteScroll>
                    </Col>
                </Row>
            </div>
            <Footer />
        </div>
    );
};

export default HomeNewsfeed;
