import React, { useState } from "react";
import { Col, Container, Row, Dropdown, Button, Form, Card, Modal } from 'react-bootstrap';
import logo from "../../assets/images/logo.png";
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import EditIcon from '../../assets/images/icons/edit.svg';
import FlashIcon from '../../assets/images/icons/flash.svg';
import TrashIcon from '../../assets/images/icons/trash-can.svg';

import './BankAccountModule.scss';

const BankAccount = () => {

     /* Add Bank Account Modal */
     const [showModal, setShow] = useState(false);
     const handleClose = () => setShow(false);
     const handleShow = () => setShow(true);

    return (
        <div className="App">
        <Header siteLogo={logo} />
            <Container>
                <div className="bankAccount-Page">
                    <Row className="main-top-padding">
                        <Col md={{ span: 12, }} className="text-center-desktop mx-auto">
                            <h2 className="pageTitle">Bank Account</h2>
                        </Col>
                    </Row>
                    <Row className="mb-5">
                        <Col xs={{ span: 12, }} className="mobile-pad-reset">
                            <div className="border-wrap">
                                <span className="bankadd-btn" onClick={handleShow}>Add a New Bank Account</span>
                            </div>
                        </Col>
                    </Row>
                    <Row className="mb-3">
                        <Col xs={{ span: 12, }} className="mobile-pad-reset">
                            <div className="border-wrap active">
                                <div className="bankAccount-block">
                                    <div className="bankAccount-block-info">
                                        <h5 className="bankAccount-block-info-title">Johnny Depp</h5>
                                        <span className="bankAccount-block-info-phone">+60 9876543210</span>
                                    </div>
                                    <p className="bankAccount-block-text-bold">Bank of Malaysia</p>
                                    <p className="bankAccount-block-text">Account number: 
                                        <strong className="bankAccount-block-text-bold m-gap">1234567890987654</strong>
                                    </p>
                                </div>
                                <Dropdown align="end" className="custom-dropdown">
                                    <Dropdown.Toggle id="dropdown-autoclose-true" className="seller-status-overflowBtn">
                                    </Dropdown.Toggle>

                                    <Dropdown.Menu className="custom-dropdown-menu">
                                    <Dropdown.Item href="#">
                                        <span className="custom-dropdown-menu-row">
                                            <span className="custom-dropdown-menu-row-icon">
                                                <img src={EditIcon} alt="" />
                                            </span>
                                            <span className="custom-dropdown-menu-row-title">Edit</span>
                                        </span>
                                    </Dropdown.Item>
                                    <Dropdown.Item href="#">
                                        <span className="custom-dropdown-menu-row">
                                            <span className="custom-dropdown-menu-row-icon">
                                                <img src={TrashIcon} alt="" />
                                            </span>
                                            <span className="custom-dropdown-menu-row-title">Delete</span>
                                        </span>
                                    </Dropdown.Item>
                                    </Dropdown.Menu>
                                </Dropdown>
                            </div>
                        </Col>
                    </Row>
                    <Row className="mb-4 justify-content-end">
                        <Col md={{ span: 6, }} xs={{ span: 12, }}>
                            <div className="d-flex mt-5">
                                <Button className="btn-base">Save</Button>
                            </div>
                        </Col>
                    </Row>
                </div>
        </Container>
        <Footer />
        {/* Add Bank Account Modal */}
        <Modal show={showModal} onHide={handleClose} className="custom-modal">
            <Modal.Header closeButton className="custom-modal-header">
            <Modal.Title className="custom-modal-header-title">Add Bank</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="addBank-details">
                    <Form.Group className="mb-3 field-group" controlId="">
                        <Form.Label>Name</Form.Label>
                        <div className="enter-bid">
                            <Form.Control
                            className={''}
                            type={'text'}
                            placeholder="Johnny Depp"
                            />
                            <span className="errorText">Invalid Error</span>
                        </div>
                    </Form.Group>
                    <Form.Group className="mb-3 field-group" controlId="">
                        <Form.Label>Phone number</Form.Label>
                        <div className="enter-bid">
                            <Form.Control
                            className={''}
                            type={'text'}
                            placeholder="+60 9876543210"
                            />
                            <span className="errorText">Invalid Error</span>
                        </div>
                    </Form.Group>
                    <Form.Group className="mb-3 field-group" controlId="">
                        <Form.Label>Bank Name</Form.Label>
                        <div className="add-product-form-field">
                            <Form.Select className={'form-select'}>
                            <option>Bank of Malaysia</option>
                            <option>Bank 1</option>
                            <option>Bank 2</option>
                            <option>Bank 3</option>
                            <option>Bank 4</option>
                            </Form.Select>
                            <span className="errorText">Invalid Error</span>
                        </div>
                    </Form.Group>
                    <Form.Group className="mb-3 field-group" controlId="">
                        <Form.Label>Account number</Form.Label>
                        <div className="enter-bid">
                            <Form.Control
                            className={''}
                            type={'text'}
                            placeholder="12334567890876"
                            />
                            <span className="errorText">Invalid Error</span>
                        </div>
                    </Form.Group>
                </div>
            </Modal.Body>
            <Modal.Footer>
            <Button variant="primary" className="btn-base" onClick={handleClose}>
                Save
            </Button>
            </Modal.Footer>
        </Modal>
    </div>
    );
}

export default BankAccount;
