import React from "react";
import { Col, Row } from "react-bootstrap";
import { useNavigate, useLocation } from 'react-router-dom';


import userIcon from './../../assets/images/header/celebrities.svg';
import favouriteIcon from './../../assets/images/header/favorite.svg';
import foundationIcon from './../../assets/images/header/Foundation.svg';
import homeIcon from './../../assets/images/header/home.svg';
import shopIcon from './../../assets/images/header/shop.svg';
import shoppingBag from './../../assets/images/header/shopping--bag.svg';
import tokenIcon from './../../assets/images/header/Token.svg';
import SearchInput from './../SearchInput/SearchInput';


// Auth-Service
import { removeUserSession, getToken } from "../../utils/AuthService";
import Logo from "../../assets/images/logo.png"

import './HeaderModule.scss';


const  Header = () => {
  const navigate = useNavigate();
  const { pathname } = useLocation();
  const token = getToken();

  const handleLogut = () =>{
    removeUserSession();
    navigate('/login');      
  }
  
  return (
    <header>
      <div className={token ? "header-center d-none" : "header-center"}>
        <img className="logo" src={Logo} alt="" />
      </div>
      <div className={token ? "header" : "header d-none"}>
        <Row className="align-items-center">
          <Col xs="4" lg="2" xl="3" xxl="3"><a href={"#"} className="header-left"><img className="logo" src={Logo} alt="" /></a></Col>
          <Col xs="12" lg="6" xl="5" xxl="6" className="order-md-3 order-lg-2 header-middle">
            <nav className="header-middle-nav">
              <ul className="header-middle-nav-main">
                <li className={pathname === "/" ? "active header-middle-nav-main-item" : "header-middle-nav-main-item"}><span onClick={() => navigate('/')} className="header-middle-nav-main-item-link"><img src={homeIcon} alt="" /></span></li>
                <li className={pathname === "/store" ? "active header-middle-nav-main-item" : "header-middle-nav-main-item"}><span onClick={() => navigate('/store')} className="header-middle-nav-main-item-link"><img src={shopIcon} alt="" /></span></li>
                <li className={pathname === "/profile" ? "active header-middle-nav-main-item" : "header-middle-nav-main-item"}><span onClick={() => navigate('/profile')} className="header-middle-nav-main-item-link"><img src={userIcon} alt="" /></span></li>
                <li className="header-middle-nav-main-item"><span className="header-middle-nav-main-item-link"><img src={foundationIcon} alt="" /></span></li>
                <li className="header-middle-nav-main-item"><span className="header-middle-nav-main-item-link"><img src={tokenIcon} alt="" /></span></li>
              </ul>
            </nav>
          </Col>
          <Col xs="8" lg="4" xl="4" xxl="3" className="order-md-2 order-lg-3">
            <div className="header-right">
              <SearchInput />
              <div className="cart-options">
                <ul className="cart-options-main">
                  <li className="cart-options-main-list"><span className="cart-options-main-list-item"><img src={favouriteIcon} alt="" /></span></li>
                  <li className="cart-options-main-list">
                    <span onClick={handleLogut} className="cart-options-main-list-item">
                      <img src={shoppingBag} alt="" />
                      <span className="notification-red">1</span>
                    </span>
                  </li>
                </ul>
              </div>
            </div>
          </Col>
        </Row>
      </div>
    </header>
  )
  }

export default Header;