import React, {useState} from "react";
import { Card, Button, Form } from "react-bootstrap";
import { Link } from "react-router-dom";
import UserImage from './../../assets/images/feed/userimage.jpg';
import Feedimage from './../../assets/images/feed/gucci-black-tshirt.jpeg';
import Swal from 'sweetalert2';
import { useNavigate } from 'react-router-dom';

// Auth-Services
import { removeUserSession, getToken } from "../../utils/AuthService"

// API-Service
import { likeUnlikeService, commentService, commentsListService } from "../../service/newsFeed.service";

import './Newsfeed.scss';

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER_IMAGE

const Newsfeed = ({feedItem}) => {
    const [totalLike, setTotalLike] = useState(feedItem?.likes);
    const [postLiked, setPostLiked] = useState(feedItem?.liked_by_me);
    const [postComments, setPostComments] = useState(feedItem?.first_five_comments);
    const [paginateOffset, setPaginateOffset] = useState(feedItem?.first_five_comments?.length);

    const [commentBody, setCommentBody] = useState("");

    const token = getToken();
    const navigate = useNavigate();

    const handleCommentBody = (e) => {
        setCommentBody(e.target.value)
    }

    const handleSubmitComment = async(event) => {
        event.preventDefault();
        const form = event.currentTarget;

        if (form.checkValidity() === false || !commentBody) {
            return;
        }
        const result = await commentService({token, id: feedItem.id, description: commentBody });
        if (result?.data?.success) {
            setCommentBody('');
            setPostComments(postComments => [...postComments, result?.data?.data])
        } else {
            setCommentBody('');
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    const handleCommentsPagination = async() => {
        
        const result = await commentsListService({token, id: feedItem.id, offset: paginateOffset});
        if (result?.data?.success) {
            setPostComments(postComments => [...postComments, ...result?.data?.data?.comments])
            setPaginateOffset(paginateOffset+20)
        } else {
            setCommentBody('');
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }

    }

    const handleLikeUnlike = async(id) => {
        const  result = await likeUnlikeService({ token, id});
        if (result?.data?.success) {
            if(result?.data?.message === "Post Liked"){
                setPostLiked(true)
                setTotalLike(totalLike+1)
            }else{
                setPostLiked(false)
                setTotalLike(totalLike-1)
            }
        } else {
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    return (
        <div className="newsFeed">
            <Card className="w-100 n-feed">
                <div className="d-flex align-items-center mb-3">
                    <figure className="user-image">
                        <img src={feedItem?.user?.image_url ? `${url}${feedItem?.user?.image_url[0]}` : UserImage} alt="" />
                    </figure>
                    <div className="User-title">
                        <h6><Link to="">{feedItem?.user?.username}</Link></h6>
                        <span>1 d</span>
                    </div>
                </div>
                <div className="border n-feed-image">
                    <Card.Img variant="top" src={feedItem?.image_urls ? `${url}${feedItem?.image_urls[0]}` : {Feedimage}} />    
                </div>
                <Card.Body className="n-feed-body">
                    <Card.Title>{feedItem?.name}</Card.Title>
                    <Card.Text>
                        {feedItem?.description}
                    </Card.Text>
                </Card.Body>
                <div className="l-comment">
                    <div className="l-comment-meta">
                        <span className="l-comment-meta-count">{totalLike} Likes</span>
                        <div className="l-comment-meta-button">
                            
                            <Button 
                                className={postLiked ? "like-btn liked" : "like-btn"}
                                onClick={()=> handleLikeUnlike(feedItem?.id)}
                            >
                                Like
                            </Button>{/* added "liked" class for click event */}
                            
                            <Button className="comment-btn">Comment</Button>
                        </div>
                    </div>
                    <div className="l-comment-box">
                        {postComments?.map((commentItem) => 
                            <div key={commentItem?.id} className="l-comment-box-post d-flex align-items-start mb-3">
                                <figure className="user-image">
                                    <img src={commentItem?.user?.image_url ? `${url}${commentItem?.user?.image_url[0]}` : UserImage} alt="" />
                                </figure>
                                <div className="User-title">
                                    <h6><Link to="">{commentItem?.user?.username}</Link></h6>
                                    <p>{commentItem?.description}</p>
                                </div>
                            </div>
                        )}

                        {
                        postComments?.length < feedItem?.comments && 
                            <Button type="button" onClick={handleCommentsPagination} className="button-text mb-3">Show More</Button>
                        }

                        <div className="l-comment-box-form d-flex align-items-center">
                            <figure className="user-image"><img src={UserImage} alt="" /></figure>
                            <div className="User-form">
                                <Form noValidate onSubmit={handleSubmitComment}>
                                    <Form.Group className="w-100">
                                        <Form.Control onChange={handleCommentBody} value={commentBody} type="text" placeholder="Write a comment..." />
                                            <Form.Text className="text-comment">
                                        </Form.Text>
                                        <Button type="submit" onClick={handleSubmitComment} className="l-comment-box-form-postBtn">Post</Button>
                                    </Form.Group>
                                </Form>
                            </div>
                        </div>
                    </div>
                </div>
            </Card>
        </div>
    );
};

export default Newsfeed;
