import React, { useState } from "react";
import './IDcounterModule.scss';

const IDcounter = () => {

    const [num, setNum]= useState(1);
    //Increment function
    const incNum =()=>{
        if(num<10)
        {
        setNum(Number(num)+1);
        }
    };
    //Decrement function
    const decNum = () => {
        if(num>1)
        {
        setNum(num - 1);
        }
    }
    const handleChange = (e)=>{
    setNum(e.target.value);
    }
    return (
        <div className="s-product-qty">
            <span className="s-product-qty-add" onClick={incNum}>+</span>
            <input type="text" className="s-product-qty-digit" value={num} onChange={handleChange}/>
            <span className="s-product-qty-del" onClick={decNum}>-</span>
        </div>
    );
};

export default IDcounter;
