import React, { useState, useEffect } from "react";
import { Button, Card, CardGroup, Col, Container, ListGroup, Row } from 'react-bootstrap';
import Swal from 'sweetalert2';
import { useNavigate } from 'react-router-dom';

import Loader from "../../components/Loader/Loader";

// API-Services
import { listOfPackagesService, buyPackageService } from "../../service/packages.service";

// Auth-Service
import { getToken, removeUserSession } from "../../utils/AuthService";

import './PackagesModule.scss';

const Packages = () => {
    const [packageList, setPackageList] = useState([]);
    const [loading, setLoading] = useState(false)
    const token = getToken();
    const navigate = useNavigate();
    const fetchPackagesData = async () => {
        setLoading(true)
        const result = await listOfPackagesService({ token})
        if (result?.data?.success) {
           setPackageList(result?.data?.data)
           setLoading(false)
        } else {
            setLoading(false)
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    useEffect(() => {
        if(packageList?.length === 0){
            fetchPackagesData();
        }
        const timeoutId = setTimeout(() => 3000);
        return function cleanup() {
            clearTimeout(timeoutId);
        }
    },[])

    const handleBuyPcakage = async(id) => {
        setLoading(true)
        const result = await buyPackageService({ token, id })
        if (result?.data?.success) {
            navigate("/404")
            setLoading(false)
         }else {
             setLoading(false)
             console.log("buyPackageService", result)
             Swal.fire({
                 title: 'Error!',
                 text: result?.response?.data?.message || "Something went wrong",
                 icon: 'error',
                 confirmButtonText: 'Try Again',
                 confirmButtonColor: "#242c44"
             })
             if (result?.response?.data?.message === "Invalid API Key") {
                 removeUserSession();
                 navigate('/login');
             }
        }
    }

    return (
        <Container>
            <div className="packages">
                <Row className="main-top-padding mb-5">
                    <Col md={{ span: 12, }} className="text-center mx-auto">{
                        <h2 className="pageTitle">Lifetime Membership</h2>
                    }</Col>
                </Row>
                {!loading ? <Row className="justify-content-center pt-3">
                    <Col lg={{ span: 10, }} xs={{ span: 10, }} xl={{ span: 8, }}>
                        <CardGroup>
                            {packageList.map((item)=>
                                <Card key={item.id}>
                                    <Card.Header>{item?.name}</Card.Header>
                                    <Card.Body>
                                        <Card.Title>${item?.price}</Card.Title>
                                        <ListGroup as="ul">
                                            {item?.features?.map((feature) => 
                                                <ListGroup.Item as="li" key={feature}>
                                                    {feature}
                                                </ListGroup.Item>
                                            )}
                                        </ListGroup>
                                    </Card.Body>
                                    <Card.Footer>
                                        <Button onClick={() => handleBuyPcakage(item.id)} className="btn-base">Join</Button>
                                    </Card.Footer>
                                </Card>
                            )}
                        </CardGroup>
                    </Col>
                </Row>
                : 
                <Row>
                    <Col lg={{ span: 12, }} xs={{ span: 12, }} xl={{ span: 12, }}>
                        <Loader />
                    </Col>
                </Row>
                }
            </div>
        </Container>
    );
}

export default Packages;
