import React, { Component } from "react";
import { Card, Button} from "react-bootstrap";
import { Link } from "react-router-dom";
import Countdown from 'react-countdown';
import './ProductofBid.scss';

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER_IMAGE

const ProductofBid = ({product}) => {
    
    const auctionEndDate = new Date(product?.auction_end_date).getTime();
    const auctionEndTime = Number(product?.auction_end_time?.split(':')[0]) * 60 * 60 * 1000 + Number(product.auction_end_time.split(':')[1]) * 60 * 1000;
    const auctionEndFinalTime = (auctionEndDate + auctionEndTime) - Date.now();

    const renderer = ({ days, hours, minutes, completed }) => {
        if (completed) {
          // Render a completed state
          return(<div className="bid-image-wrap-timestamp">
                <div className="bid-image-wrap-timestamp-post">
                    <span className="bid-image-wrap-timestamp-post-days">00</span>
                    <span className="bid-image-wrap-timestamp-post-text">Days</span>
                </div>
                <div className="bid-image-wrap-timestamp-post">
                    <span className="bid-image-wrap-timestamp-post-days">00</span>
                    <span className="bid-image-wrap-timestamp-post-text">Hours</span>
                </div>
                <div className="bid-image-wrap-timestamp-post">
                    <span className="bid-image-wrap-timestamp-post-days">00</span>
                    <span className="bid-image-wrap-timestamp-post-text">Minutes</span>
                </div>
            </div>
            )
        } else {
          // Render a countdown
          return(<div className="bid-image-wrap-timestamp">
                    <div className="bid-image-wrap-timestamp-post">
                        <span className="bid-image-wrap-timestamp-post-days">{days}</span>
                        <span className="bid-image-wrap-timestamp-post-text">Days</span>
                    </div>
                    <div className="bid-image-wrap-timestamp-post">
                        <span className="bid-image-wrap-timestamp-post-days">{hours}</span>
                        <span className="bid-image-wrap-timestamp-post-text">Hours</span>
                    </div>
                    <div className="bid-image-wrap-timestamp-post">
                        <span className="bid-image-wrap-timestamp-post-days">{minutes}</span>
                        <span className="bid-image-wrap-timestamp-post-text">Minutes</span>
                    </div>
                </div>
            )
        }
    };

    return (
        <div className="productWrap">
            <Card className="product-widget">
                <div className="bid-image-wrap">
                    <span className="favouriteWish"></span>
                    <Link to="/single-product-bid" state={{ productID: product?.id }}>
                        <Card.Img variant="top" className="product-widget-image" src={`${url}${product.image_urls[0]}`} />
                    </Link>
                    <Countdown 
                    date={Date.now() + auctionEndFinalTime}
                    renderer={renderer} />
                </div>
                <Card.Body className="product-widget-body">
                    <Card.Title className="product-widget-body-title">
                        <Link to="/single-product-bid" state={{ productID: product?.id }}>{product.name}</Link>
                    </Card.Title>
                    <Card.Text className="product-widget-body-tag">{product?.user?.username}</Card.Text>
                    <div className="product-widget-body-bid">
                        <span className="product-widget-body-tag">Current Bid:</span>
                        <span className="product-widget-body-price">${product.price}.00</span>
                    </div>
                    <Button variant="primary" className="btn-base">Bid</Button>
                </Card.Body>
            </Card>
        </div>
    );
};

export default ProductofBid;
