/**
* @function addToCartService
* @param {token, product_id, quantity}
* @description This function is used add to cart product
*/

export const addToCartService = async({ token, product_id, quantity }) =>{
    try {
        const result = await axios.post(`${url}/cart`,{
            product_id,
            quantity
        },{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}


