/**
* @function placeBidService
* @param {token, is_anonymous}
* @description This function is used to place a Bid to auctioned product
*/

export const placeBidService = async({ token, is_anonymous, id }) =>{
    try {
        const result = await axios.post(`${url}/product/${id}/bid`,{
            is_anonymous
        },{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}

