import React, { useState, useEffect } from "react";
import { Button, Col, Container, Form, Row } from 'react-bootstrap';
import logo from "../../assets/images/logo.png";
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';

import './AddProductModule.scss';

const AddProduct = () => {

    return (
        <div className="App">
        <Header siteLogo={logo} />
            <Container>
                <div className="addProductpage">
                    <Row className="main-top-padding">
                        <Col md={{ span: 12, }} className="text-center-desktop mx-auto">
                            <h2 className="pageTitle">Add Products</h2>
                        </Col>
                    </Row>
                    <Row className="mb-bottom">
                        <Col xs={{ span: 12, }}>
                            <div className="p-auction-label">
                                <h6 className="p-auction-label-title">Product for Auction</h6>
                                <span className="p-auction-label-checkbox">
                                    <Form>
                                        <Form.Check 
                                            type="switch"
                                            label=""
                                        />
                                    </Form>
                                </span>
                            </div>
                        </Col>
                    </Row>
                    <Row className="mb-bottom">
                        <Col md={{ span: 5, }} lg={{ span: 4, }} xl={{ span: 3, }} xs={{ span: 12, }}>
                            <Form.Group controlId="formFile" className="uploadImage">
                                <Form.Label>
                                    <span>Drag image(s) here</span> <span>or</span> <strong>Browse image</strong></Form.Label>
                                <Form.Control type="file" />
                            </Form.Group>
                        </Col>
                        <Col md={{ span: 7, }} lg={{ span: 6, }} xs={{ span: 12, }}>
                            <Form.Group className="mb-4 field-group" controlId="">
                                <Form.Label>Name</Form.Label>
                                <div className="add-product-form-field">
                                    <Form.Control
                                    className={''}
                                    type={'text'}
                                    placeholder=""
                                    />
                                </div>
                            </Form.Group>
                            <Form.Group className="mb-4 field-group" controlId="">
                                <Form.Label>Description</Form.Label>
                                <div className="add-product-form-field">
                                    <Form.Control as="textarea" rows={5} />
                                </div>
                            </Form.Group>
                        </Col>
                    </Row>
                    <Row>
                        <Col md={{ span: 6, }} xs={{ span: 12, }}>
                            <div className="add-product-form">
                                <Form.Group className="mb-4 field-group" controlId="">
                                    <Form.Label>Product Price</Form.Label>
                                    <div className="add-product-form-field price-field">
                                        <Form.Control
                                        className={''}
                                        type={'text'}
                                        placeholder="250.00"
                                        />
                                    </div>
                                </Form.Group>
                            </div>
                        </Col>
                        <Col md={{ span: 6, }} xs={{ span: 12, }}>
                            <div className="add-product-form">
                                <Form.Group className="mb-4 field-group">
                                    <Form.Label>Brand Name</Form.Label>
                                    <div className="add-product-form-field select-group">
                                        <Form.Select className={'form-select'}>
                                        <option>Louis Vuitton</option>
                                        <option>Louis Vuitton</option>
                                        <option>Louis Vuitton</option>
                                        <option>Louis Vuitton</option>
                                        </Form.Select>
                                    </div>
                                </Form.Group>
                            </div>
                        </Col>
                    </Row>
                    <Row className="border-bottom mb-bottom-frow">
                        <Col md={{ span: 6, }} xs={{ span: 12, }}>
                            <div className="add-product-form">
                                <Form.Group className="mb-mobile field-group">
                                    <Form.Label>Product Category</Form.Label>
                                    <div className="add-product-form-field select-group">
                                        <Form.Select className={'form-select'}>
                                        <option>Louis Vuitton</option>
                                        <option>Louis Vuitton</option>
                                        <option>Louis Vuitton</option>
                                        <option>Louis Vuitton</option>
                                        </Form.Select>
                                    </div>
                                </Form.Group>
                            </div>
                        </Col>
                        <Col md={{ span: 6, }} xs={{ span: 12, }}>
                            <div className="add-product-form">
                                <Form.Group className="mb-mobile field-group" controlId="">
                                    <Form.Label>Stock Quantity</Form.Label>
                                    <div className="add-product-form-field">
                                        <Form.Control
                                        className={''}
                                        type={'text'}
                                        placeholder="250.00"
                                        />
                                    </div>
                                </Form.Group>
                            </div>
                        </Col>
                    </Row>
                    <Row className="border-bottom mb-bottom-frow">
                        <Col md={{ span: 6, }} xs={{ span: 12, }}>
                            <div className="block-level">
                                <h6 className="block-level-title">Product variation (Optional)</h6>
                                <span className="block-level-addkey">+Add</span>
                            </div>
                            <div className="block-level-field">
                                <div className="add-product-form">
                                    <Form.Group className="mb-mobile field-group" controlId="">
                                        <Form.Label>Variation</Form.Label>
                                        <div className="add-product-form-field">
                                            <Form.Control
                                            className={''}
                                            type={'text'}
                                            placeholder="Louis Vuitton"
                                            />
                                        </div>
                                    </Form.Group>
                                </div>
                                <div className="add-product-form">
                                    <Form.Group className="mb-mobile field-group" controlId="">
                                        <Form.Label>Price</Form.Label>
                                        <div className="add-product-form-field price-field">
                                            <Form.Control
                                            className={''}
                                            type={'text'}
                                            placeholder=""
                                            />
                                        </div>
                                    </Form.Group>
                                </div>
                            </div>
                        </Col>
                        <Col md={{ span: 6, }} xs={{ span: 12, }}>
                        <div className="block-level">
                                <h6 className="block-level-title">Product add-ons (Optional)</h6>
                                <span className="block-level-addkey">+Add</span>
                            </div>
                            <div className="block-level-field">
                                <div className="add-product-form">
                                    <Form.Group className="mb-mobile field-group" controlId="">
                                        <Form.Label>Variation</Form.Label>
                                        <div className="add-product-form-field">
                                            <Form.Control
                                            className={''}
                                            type={'text'}
                                            placeholder="Louis Vuitton"
                                            />
                                        </div>
                                    </Form.Group>
                                </div>
                                <div className="add-product-form">
                                    <Form.Group className="mb-mobile field-group" controlId="">
                                        <Form.Label>Price</Form.Label>
                                        <div className="add-product-form-field price-field">
                                            <Form.Control
                                            className={''}
                                            type={'text'}
                                            placeholder=""
                                            />
                                        </div>
                                    </Form.Group>
                                </div>
                            </div>
                        </Col>
                    </Row>
                    <Row className="mb-5 pb-5">
                        <Col md={{ span: 6, }} xs={{ span: 12, }}>
                            <div className="block-level">
                                <h6 className="block-level-title">Charity donation</h6>
                            </div>
                            <div className="block-level-field">
                                <div className="add-product-form">
                                    <Form.Group className="mb-mobile field-group" controlId="">
                                        <Form.Label>Charity foundation</Form.Label>
                                        <div className="add-product-form-field select-group">
                                            <Form.Select className={'form-select'}>
                                            <option>Louis Vuitton</option>
                                            <option>Louis Vuitton</option>
                                            <option>Louis Vuitton</option>
                                            <option>Louis Vuitton</option>
                                            </Form.Select>
                                        </div>
                                    </Form.Group>
                                </div>
                                <div className="add-product-form">
                                    <Form.Group className="mb-mobile field-group" controlId="">
                                        <Form.Label>Price</Form.Label>
                                        <div className="add-product-form-field percent-field">
                                            <Form.Control
                                            className={''}
                                            type={'text'}
                                            placeholder="10"
                                            />
                                        </div>
                                    </Form.Group>
                                </div>
                            </div>
                            <div className="d-flex mt-5">
                                <Button className="btn-base">Add Product</Button>
                            </div>
                        </Col>
                    </Row>
                </div>
        </Container>
        <Footer />
    </div>
    );
}

export default AddProduct;
