import axios from "axios"

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER

/**
* @function userProfileService
* @param {token, id}
* @description This function is used to get user profile data
*/

export const userProfileService = async({ token, id }) =>{
    try {
        const result = await axios.get(`${url}/user-profile/${id}`,{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}