import React, {useState, useEffect} from "react";
import {Container, Row, Col } from 'react-bootstrap';
import {useSelector} from "react-redux"

import logo from "../../assets/images/logo.png";
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';

import SearchInput from '../../components/SearchInput/SearchInput';
import Searchnotfound from '../../components/SearchnotFound/SearchnotFound';
import Loader from '../../components/Loader/Loader';

import ProductofBid from '../../components/Products/ProductofBid';
import ProductofList from '../../components/Products/ProductofList';
import Brand from "../../components/Brand/Brand"


// Redux-Functions
import {selectUsers, selectProducts, selectBrands, selectSearchTerm} from "../../redux/searchResultSlice"

import './Searchresultmodule.scss';

const Seachresult = () => {

        const [noserchResult, setNoserchResult] = useState(false)
        const [lading, setLading] = useState(true)

        const users = useSelector(selectUsers);
        const products = useSelector(selectProducts);
        const brands = useSelector(selectBrands);
        const searchTerm = useSelector(selectSearchTerm);
        console.log("users", users)
        console.log("products", products)
        console.log("brands", brands)
        console.log("searchTerm", searchTerm)

        useEffect(() =>{
            if(users?.length === 0 && products?.length === 0 && brands?.length === 0){
                setNoserchResult(true)
            }
            const timeoutId = setTimeout(() => setLading(false), 5000);
            return function cleanup() {
                clearTimeout(timeoutId);
            }
        },[users, products, brands])

        return (
            <div className="App">
                <Header siteLogo={logo} />
                    <Container>
                        {lading ? <Loader/> : 
                        <div className="s-category page-top">
                            {!noserchResult ? 
                                <div>
                                    <Row className="position-relative">
                                        <Col xs={{ span: 12, }}>
                                            <div className="searchMobile">
                                                <SearchInput />
                                            </div>
                                            <div className="showingText-search">
                                                <span className="showingText-label mr-2">Showing Products : </span>
                                                <span className="showingText-count">{products?.length} result <span className="searchLabeldesktop">for "{searchTerm}"</span></span>
                                            </div>
                                            <div className="searchLabelmobile">T-shirt</div>
                                            
                                            <div className="pb-5 mob-pad-top-20 reset-col-padding-mobile">
                                                <Row>
                                                    {products?.map(productItem => 
                                                        <Col key={productItem.id} md={{ span: 6, }} xs={{ span: 6, }} lg={{ span: 3, }}>
                                                            { productItem?.is_auction ? 
                                                                    <ProductofBid product={productItem} /> :
                                                                    <ProductofList product={productItem} /> 
                                                            }
                                                        </Col>
                                                    )}
                                                </Row>
                                            </div>
                                        </Col>
                                    </Row>
                                    <Row className="position-relative">
                                        <Col xs={{ span: 12, }}>
                                            <div className="searchMobile">
                                                <SearchInput />
                                            </div>
                                            <div className="showingText-search">
                                                <span className="showingText-label mr-2">Showing Users : </span>
                                                <span className="showingText-count">{users?.length} result <span className="searchLabeldesktop">for "{searchTerm}"</span></span>
                                            </div>
                                            <div className="searchLabelmobile">T-shirt</div>
                                            
                                            <div className="pb-5 mob-pad-top-20 reset-col-padding-mobile">
                                                <Row>
                                                    {users?.map(userItem => 
                                                        <Col key={userItem.id} md={{ span: 6, }} xs={{ span: 6, }} lg={{ span: 3, }}>
                                                           <h2>{userItem?.username}</h2>
                                                           <h2>{userItem?.email}</h2>
                                                           <h2>{userItem?.is_active}</h2>
                                                        </Col>
                                                    )}
                                                </Row>
                                            </div>
                                        </Col>
                                    </Row>
                                    <Row className="position-relative">
                                        <Col xs={{ span: 12, }}>
                                            <div className="searchMobile">
                                                <SearchInput />
                                            </div>
                                            <div className="showingText-search">
                                                <span className="showingText-label mr-2">Showing Brands : </span>
                                                <span className="showingText-count">{brands?.length} result <span className="searchLabeldesktop">for "{searchTerm}"</span></span>
                                            </div>
                                            <div className="searchLabelmobile">T-shirt</div>
                                            
                                            <div className="pb-5 mob-pad-top-20 reset-col-padding-mobile">
                                                <Row>
                                                    {brands?.map(brandItem => 
                                                        <Col key={brandItem.id} md={{ span: 6, }} xs={{ span: 6, }} lg={{ span: 3, }}>
                                                            <Brand brandItem={brandItem}/>
                                                        </Col>
                                                    )}
                                                </Row>
                                            </div>
                                        </Col>
                                    </Row>
                                </div> 
                            :
                            <Row>
                                <Col md={{ span: 12, }} xs={{ span: 12, }} lg={{ span: 12, }}>
                                    <Searchnotfound searchTerm={searchTerm} />
                                </Col>
                            </Row>
                            }
                        </div>
                        }
                    </Container>
                <Footer />
            </div>
        );
};

export default Seachresult;
