import React, { useEffect, useState } from "react";
import { Col, Container, Row, ListGroup } from 'react-bootstrap';
import { Link } from "react-router-dom";
import Swal from 'sweetalert2';
import { useNavigate } from 'react-router-dom';
import InfiniteScroll from 'react-infinite-scroll-component';

import ProductofBid from '../../components/Products/ProductofBid';
import ProductofList from '../../components/Products/ProductofList';
import Loader from "../../components/Loader/Loader";

import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';

import Rangeslider from "../../assets/images/range-slider.svg";
import logo from "../../assets/images/logo.png";

// API-Service
import { allProductCategoryService, singleCategoryProductService } from "../../service/product.service";

// Auth-Service
import { removeUserSession, getToken } from "../../utils/AuthService";

import './SinglecategoryModule.scss';

const Singlecategory = () => {  

    const [categoryList, setCategoryList] = useState([]);
    const [productList, setProductList] = useState([]);

    const [productLoading, setProductLoading] = useState(true);
    const [cateGoryloading, setCateGoryloading] = useState(false);

    // For pagintaion and infinite-scroll 
    const [isHasmore, setIsHasmore] = useState(true);
    const [pageCounter, setPageCounter] = useState(1);
    const [catagoryId, setCatagoryId] = useState(null);

    const token = getToken();
    const navigate = useNavigate();

    const fetchProductCategoryData = async () => {
        setCateGoryloading(true)
        const result = await allProductCategoryService({ token })
        if (result?.data?.success) {
            setCategoryList(result?.data?.data?.product_categories)
            setCateGoryloading(false)
        } else {
            setCateGoryloading(false)
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    useEffect(() => {
        if (categoryList?.length === 0) {
            fetchProductCategoryData();
        }
        const timeoutId = setTimeout(() => 3000);
        return function cleanup() {
            clearTimeout(timeoutId);
        }
    }, [])


    const handleGetProductOnScroll = async(e, page=1) => {
        setCatagoryId(e);
        const  result = await singleCategoryProductService({ token, id: e , perPage: 5, page});
        if (result?.data?.success) {
            if (productList?.length >= result?.data?.data?.products?.total) {
                setIsHasmore(false);
                return;
            }
            setProductList(productList?.concat(result?.data?.data?.products?.data));
            setProductLoading(false);
            setPageCounter(pageCounter+1);
        } else {
            setProductLoading(false)
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    const handleGetProductOnClick = async(e, page=1) => {
        setCatagoryId(e);
        setIsHasmore(true);
        setPageCounter(2);
        const result = await singleCategoryProductService({ token, id: e , perPage: 5, page});
        if (result?.data?.success) {
            setProductList(result?.data?.data?.products?.data);
            setProductLoading(false);
        } else {
            setProductLoading(false)
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    useEffect(() => {
        
        if (categoryList?.length !== 0) {
            handleGetProductOnClick(categoryList[0]?.id);
        }

        const timeoutId = setTimeout(() => 3000);
        return function cleanup() {
            clearTimeout(timeoutId);
        }
    }, [categoryList])


    return (
        <div className="App">
            <Header siteLogo={logo} />
            <Container>
                <div className="s-category page-top">
                    <Row className="mb-5">
                        <Col md={{ span: 12, }} className="text-center mx-auto">
                            <h2 className="title-with-dash">Shop By Categories</h2>
                        </Col>
                    </Row>
                    <Row className="position-relative">
                        <Col md={{ span: 3, }}>
                            <div className="border-right">
                                <div className="mobile-category">
                                    <div className="mobile-category-menuicon">
                                        <i className="bi bi-list"></i>
                                    </div>
                                    <h3 className="category-widget-title">Categories</h3>
                                </div>
                                <div className="category-widget-panel">
                                    <div className="category-widget">
                                        <h3 className="category-widget-title">Categories</h3>
                                        {cateGoryloading ? 
                                            <Loader/> :
                                            <ListGroup className="category-widget-list">
                                               {categoryList?.map(item =>
                                                <ListGroup.Item onClick={()=> handleGetProductOnClick(item.id)} key={item.id} className="category-widget-list-item active"><Link to=''>{item.name}</Link></ListGroup.Item>
                                               )}
                                            </ListGroup>
                                        }
                                    </div>
                                    <div className="category-widget">
                                        <h3 className="category-widget-title">Price</h3>
                                        <div>
                                            <img src={Rangeslider} alt="" />
                                        </div>
                                        <span className="range-amount">$15  -  $550</span>
                                    </div>
                                </div>
                            </div>
                        </Col>
                        <Col md={{ span: 9, }} xs={{ span: 12, }}>
                            <div className="showingText">
                                <span className="showingText-label">Showing : </span>
                                <span className="showingText-count">{productList?.length} products</span>
                            </div>
                            <div className="pb-5 mob-pad-top-20 reset-col-padding-mobile">
                                
                                {productLoading ? 
                                    <Loader/> : 
                                        <InfiniteScroll
                                            dataLength={productList.length}
                                            next={() =>handleGetProductOnScroll(catagoryId, pageCounter)}
                                            hasMore={isHasmore}
                                            loader={<Loader/>}
                                            endMessage={
                                                <p style={{ textAlign: 'center' }}>
                                                    <b>Yay! You have seen it all</b>
                                                </p>
                                            }
                                        >
                                            <Row className="reset-col-padding-mobile">
                                                {productList?.map(productItem =>  productItem?.is_auction ? 
                                                    <Col key={productItem.id} md={{ span: 6, }} xs={{ span: 6, }} lg={{ span: 4,}}>
                                                        <ProductofBid  product={productItem} />
                                                    </Col>
                                                        :
                                                        <Col key={productItem.id} md={{ span: 6, }} xs={{ span: 6, }} lg={{ span: 4,}}>
                                                        <ProductofList product={productItem} /> 
                                                    </Col>
                                                )}
                                            </Row>
                                        </InfiniteScroll>
                                    }
                            </div>
                        </Col>
                    </Row>
                </div>
            </Container>
            <Footer />
        </div>
    );
};

export default Singlecategory;
