import React, { useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import './ProductImageSliderModule.scss';
import Singleproductimg from '../../assets/images/feed/gucci-black-tshirt.jpeg';
import Singleproductimg2 from '../../assets/images/feed/oversize-drop-shirt.jpeg';

// Import Swiper styles
import "swiper/css";
import "swiper/css/free-mode";
import "swiper/css/navigation";
import "swiper/css/thumbs";

import { FreeMode, Navigation, Thumbs } from "swiper";

const ProductImageSlider = () => {
    const [thumbsSwiper, setThumbsSwiper] = useState();
    return (
        <div className="productSwiper-wrap">
            <Swiper
                onSwiper={setThumbsSwiper}
                loop={false}
                spaceBetween={10}
                slidesPerView={4}
                freeMode={true}
                watchSlidesProgress={true}
                modules={[FreeMode, Navigation, Thumbs]}
                className="swiper-thumb"
            >
                <SwiperSlide className="swiper-thumb-slide">
                <img src={Singleproductimg} alt="" />
                </SwiperSlide>
                <SwiperSlide className="swiper-thumb-slide">
                <img src={Singleproductimg2} alt="" />
                </SwiperSlide>
                <SwiperSlide className="swiper-thumb-slide">
                <img src={Singleproductimg} alt="" />
                </SwiperSlide>
                <SwiperSlide className="swiper-thumb-slide">
                <img src={Singleproductimg2} alt="" />
                </SwiperSlide>
                <SwiperSlide className="swiper-thumb-slide">
                <img src={Singleproductimg} alt="" />
                </SwiperSlide>
                <SwiperSlide className="swiper-thumb-slide">
                <img src={Singleproductimg2} alt="" />
                </SwiperSlide>
                <SwiperSlide className="swiper-thumb-slide">
                <img src={Singleproductimg} alt="" />
                </SwiperSlide>
            </Swiper>
            <div className="productSwiper-wrap-right">
                <Swiper
                    loop={false}
                    spaceBetween={10}
                    navigation={true}
                    //thumbs={{ swiper: thumbsSwiper }}
                    modules={[FreeMode, Navigation, Thumbs]}
                    className="swiper-slide2"
                >
                    <SwiperSlide>
                    <img src={Singleproductimg} alt="" />
                    </SwiperSlide>
                    <SwiperSlide>
                    <img src={Singleproductimg2} alt="" />
                    </SwiperSlide>
                    <SwiperSlide>
                    <img src={Singleproductimg} alt="" />
                    </SwiperSlide>
                    <SwiperSlide>
                    <img src={Singleproductimg2} alt="" />
                    </SwiperSlide>
                    <SwiperSlide>
                    <img src={Singleproductimg} alt="" />
                    </SwiperSlide>
                    <SwiperSlide>
                    <img src={Singleproductimg2} alt="" />
                    </SwiperSlide>
                </Swiper>
            </div>
        </div>
    );
};

export default ProductImageSlider;
