import React, { useState } from 'react';
import { Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
import { useDispatch } from 'react-redux';


// API-Service
import { searchService } from '../../service/search.service';

// Auth-Service
import { getToken, removeUserSession } from '../../utils/AuthService';

// Reduc-Functions
import { 
    setUsersList,
    setProductsList,
    setBrandsList,
    setSearchTerm
} from "../../redux/searchResultSlice"

import searchIcon from './../../assets/images/header/search-mobile.svg';
import './SearchModule.scss';

const SearchInput = () => {

    const [searchTag, setSearchTag] = useState("")
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const token = getToken();

    const handleChange = (e) => {
        setSearchTag(e.target.value)
    }

   /**
   * @function fetchSearchResult
   * @param event
   * @description This function is used to fetch serach result
   */

    const fetchSearchResult = async (event) => {
        event.preventDefault();
        const form = event.currentTarget;

        if (form.checkValidity() === false || !searchTag) {
            return;
        }

        const result = await searchService({ token, term: searchTag })
        if (result?.data?.success) {
            dispatch(setUsersList(result?.data?.data?.users))
            dispatch(setProductsList(result?.data?.data?.products))
            dispatch(setBrandsList(result?.data?.data?.brands))
            dispatch(setSearchTerm(searchTag))
            setSearchTag('')
            navigate('/search-result')
        } else {
            setSearchTag('')
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }


    return (
        <div className='searchWrap'>
            <div className="searchIcon"><img src={searchIcon} alt="" /></div>
            <Form noValidate onSubmit={fetchSearchResult}>
                <div className="s-input">
                    <input type="text" onChange={handleChange} className="s-input-field" placeholder='Search' />
                </div>
            </Form>
        </div>
    );
};

export default SearchInput;
