import React, { useState } from "react";
import { Tabs, Tab, Col, Row, Button, Modal, Card, Dropdown, Form } from 'react-bootstrap';
import logo from "../../assets/images/logo.png"
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import Newsfeed from '../../components/Newsfeed/Newsfeed';
import OrderManagement from "../../components/OrderManagement/OrderManagement";
import PurchasedOrder from "../../components/PurchasedOrder/PurchasedOrder";
import Product from '../../components/ProductImage/ProductImage';
import ProductImage from '../../assets/images/gucci-black-tshirt.jpg';
import EditIcon from '../../assets/images/icons/edit.svg';
import CharityIcon from '../../assets/images/icons/charity.svg';
import AddressIcon from '../../assets/images/icons/address.svg';
import PasswordIcon from '../../assets/images/icons/password.svg';
import AccountIcon from '../../assets/images/icons/account.svg';
import LogoutIcon from '../../assets/images/icons/logout.svg';
import Pagination from 'react-bootstrap/Pagination';

import OtherProfile from "../../assets/images/profile.png";

import './MyprofileProductsVerifySellerModule.scss';

const OtherProfileNewsfeed = () => {

    /* Dialog Modal */
    const [showModal, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    /* Charity Modal */
    const [charityModal, setShowcharity] = useState(false);
    const charityClose = () => setShowcharity(false);
    const charityShow = () => setShowcharity(true);

    /* Myprofile Modal */
    const [mprofileModal, setShowmprofile] = useState(false);
    const mprofileClose = () => setShowmprofile(false);
    const mprofileShow = () => setShowmprofile(true);

    /* Change Password Modal */
    const [changePasswordModal, setShowpassword] = useState(false);
    const mpasswordClose = () => setShowpassword(false);
    const mpasswordShow = () => setShowpassword(true);

     /* withdraw Modal */
     const [withdrawdModal, setShowwithdraw] = useState(false);
     const withdrawClose = () => setShowwithdraw(false);
     const withdrawShow = () => setShowwithdraw(true);

    return (
        <div className="App">
            <Header siteLogo={logo} />
            <div className="container">
                <Row className="mb-5 main-top-padding">
                    <Col lg={{ span: 10, }} md={{ span: 8, }} xs={{ span: 12, }} className="mx-auto reset-padding-mobile">
                        <div className="otherProfilefeed">
                            <div className="ou-profile">
                                <div className="ou-profile-cover">
                                    <div className="edit-cover">
                                        <span className="edit-cover-text">Edit Cover Photo</span>
                                    </div>
                                </div>
                                <div className="ou-profile-cont">
                                    <div className="ou-profile-cont-info">
                                        <div className="ou-profile-cont-info-image">
                                            <img src={OtherProfile} alt="" />
                                        </div>
                                        <div className="ou-profile-cont-info-cont">
                                            <h2 className="ou-profile-cont-info-cont-name">Johnny Depp</h2>
                                            <span className="ou-profile-cont-info-cont-value">150 Products</span>
                                        </div>
                                    </div>
                                    <div className="ou-profile-cont-withtext">
                                        <div className="ou-profile-cont-desc">
                                            <p className="ou-profile-cont-desc-text">Lorem ipsum dolor sit amet,
        nonumy eirmod tempor invidunt ut labore et dolore magna 
        aliquyam erat, sed diam voluptua. At vero eos.</p>
                                        </div>
                                        <div className="seller-status">
                                            <span className="seller-status-active btn-base">+Add Product</span>
                                            <span className="seller-status-active btn-base green-bg">Verified Seller</span>
                                            <Dropdown align="end" className="custom-dropdown">
                                                <Dropdown.Toggle id="dropdown-autoclose-true" className="seller-status-overflowBtn">
                                                </Dropdown.Toggle>

                                                <Dropdown.Menu className="custom-dropdown-menu">
                                                <Dropdown.Item href="#">
                                                    <span className="custom-dropdown-menu-row">
                                                        <span className="custom-dropdown-menu-row-icon">
                                                            <img src={EditIcon} alt="" />
                                                            </span>
                                                        <span className="custom-dropdown-menu-row-title" onClick={mprofileShow}>Edit Profile</span>
                                                    </span>
                                                </Dropdown.Item>
                                                <Dropdown.Item href="#">
                                                    <span className="custom-dropdown-menu-row" onClick={charityShow}>
                                                        <span className="custom-dropdown-menu-row-icon">
                                                            <img src={CharityIcon} alt="" />
                                                            </span>
                                                        <span className="custom-dropdown-menu-row-title">Propose charity</span>
                                                    </span>
                                                </Dropdown.Item>
                                                <Dropdown.Item href="#">
                                                    <span className="custom-dropdown-menu-row">
                                                        <span className="custom-dropdown-menu-row-icon">
                                                            <img src={AddressIcon} alt="" />
                                                            </span>
                                                        <span className="custom-dropdown-menu-row-title">My Addresses</span>
                                                    </span>
                                                </Dropdown.Item>
                                                <Dropdown.Item href="#">
                                                    <span className="custom-dropdown-menu-row" onClick={mpasswordShow}>
                                                        <span className="custom-dropdown-menu-row-icon">
                                                            <img src={PasswordIcon} alt="" />
                                                            </span>
                                                        <span className="custom-dropdown-menu-row-title">Change Password</span>
                                                    </span>
                                                </Dropdown.Item>
                                                <Dropdown.Item href="#">
                                                    <span className="custom-dropdown-menu-row">
                                                        <span className="custom-dropdown-menu-row-icon">
                                                            <img src={AccountIcon} alt="" />
                                                            </span>
                                                        <span className="custom-dropdown-menu-row-title">Bank Account</span>
                                                    </span>
                                                </Dropdown.Item>
                                                <Dropdown.Item href="#">
                                                    <span className="custom-dropdown-menu-row">
                                                        <span className="custom-dropdown-menu-row-icon">
                                                            <img src={LogoutIcon} alt="" />
                                                            </span>
                                                        <span className="custom-dropdown-menu-row-title">Log Out</span>
                                                    </span>
                                                </Dropdown.Item>
                                                </Dropdown.Menu>
                                            </Dropdown>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="myProfile-tab">
                            <Tabs defaultActiveKey="products" id="otheruserprofile-newsfeed" className="myProfile-tab-group">
                                <Tab eventKey="newsfeed" title="Newsfeed" className="myProfile-tab-group-tab">
                                    <Row className="mx-0">
                                        <Col lg={{ span: 9, }} xs={{ span: 12, }} className="mx-auto pb-5 reset-padding-mobile">
                                            <div className="btn-base c-newPost for-desktop">
                                                <span className="c-newPost-text" onClick={handleShow}>Create New Post</span>
                                            </div>
                                            <div className="btn-base c-newPost for-mobile">
                                                <span className="c-newPost-text" onClick={handleShow}>Post</span>
                                            </div>
                                            <Newsfeed />
                                        </Col>
                                    </Row>
                                </Tab>
                                <Tab eventKey="products" title="Products" className="myProfile-tab-group-tab">
                                    <div className="px-4 reset-col-padding pb-5">
                                        <Row>
                                            <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                                <Product />
                                            </Col>
                                            <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                                <Product />
                                            </Col>
                                            <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                                <Product />
                                            </Col>
                                            <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                                <Product />
                                            </Col>
                                            <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                                <Product />
                                            </Col>
                                            <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                                <Product />
                                            </Col>
                                            <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                                <Product />
                                            </Col>
                                            <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                                <Product />
                                            </Col>
                                            <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                                <Product />
                                            </Col>
                                        </Row>
                                    </div>
                                    <div className="px-4 no-products pb-5">
                                        <Row className="justify-content-center">
                                            <Col lg={{ span: 9, }} xs={{ span: 12, }} className="text-center">
                                                <h6 className="no-products-title">There are no products</h6>
                                                <p className="no-products-text">To add a product Please activate seller</p>
                                            </Col>
                                        </Row>
                                        <Row className="justify-content-center">
                                            <Col lg={{ span: 3, }} md={{ span: 5, }} xs={{ span: 12, }}>
                                                <Button className="btn-base no-products-btn">Activate seller status</Button>
                                            </Col>
                                        </Row>
                                    </div>
                                    <div className="px-4 no-products pb-5 mb-5">
                                        <Row className="justify-content-center">
                                            <Col lg={{ span: 9, }} xs={{ span: 12, }} className="text-center">
                                                <h6 className="no-products-title">Seller activation pending approval</h6>
                                            </Col>
                                        </Row>
                                    </div>
                                </Tab>
                                <Tab eventKey="order" title="Order" className="myProfile-tab-group-tab">
                                    <OrderManagement />
                                    <OrderManagement />
                                    <OrderManagement />
                                    <OrderManagement />
                                </Tab>
                                <Tab eventKey="purchasehistory" title="Purchase History" className="myProfile-tab-group-tab px-0 pt-5">
                                    <PurchasedOrder />
                                </Tab>
                                <Tab eventKey="wallet" title="Wallet" className="myProfile-tab-group-tab pt-5">
                                    <div className="wallet-apart">
                                        <Row>
                                            <Col lg={{ span: 5, }} xs={{ span: 12, }} className="reset-pad">
                                                <div className="p-wallet">
                                                    <h6 className="p-wallet-title">Wallet</h6>
                                                    <span className="p-wallet-price">$2500.50</span>
                                                    <span className="p-wallet-amount">Available amount</span>

                                                    <Button className="btn-base" onClick={withdrawShow}>Withdraw</Button>
                                                </div>
                                            </Col>
                                            <Col lg={{ span: 7, }} xs={{ span: 12, }} className="reset-pad">
                                                <div className="W-trans">
                                                    <h3 className="W-trans-title">Transactions</h3>
                                                    <div className="W-trans-post">
                                                        <div className="W-trans-post-flex">
                                                            <span className="W-trans-post-flex-title">Ling Tan</span>
                                                            <span className="W-trans-post-flex-price">$250.00</span>
                                                        </div>
                                                        <span className="W-trans-post-text">Iconic Link Watch</span>
                                                        <span className="W-trans-post-date">24 march 2022, 10:30 am</span>
                                                    </div>
                                                    <div className="W-trans-post">
                                                        <div className="W-trans-post-flex">
                                                            <span className="W-trans-post-flex-title">Alexandre Selinelli</span>
                                                            <span className="W-trans-post-flex-price">$250.00</span>
                                                        </div>
                                                        <span className="W-trans-post-text">Apple iPhone 13</span>
                                                        <span className="W-trans-post-date">24 march 2022, 10:30 am</span>
                                                    </div>
                                                    <div className="W-trans-post">
                                                        <div className="W-trans-post-flex">
                                                            <span className="W-trans-post-flex-title">Ling Tan</span>
                                                            <span className="W-trans-post-flex-price">$250.00</span>
                                                        </div>
                                                        <span className="W-trans-post-text">Iconic Link Watch</span>
                                                        <span className="W-trans-post-date">24 march 2022, 10:30 am</span>
                                                    </div>
                                                    <div className="W-trans-post">
                                                        <div className="W-trans-post-flex">
                                                            <span className="W-trans-post-flex-title">Ling Tan</span>
                                                            <span className="W-trans-post-flex-price">$250.00</span>
                                                        </div>
                                                        <span className="W-trans-post-text">Iconic Link Watch</span>
                                                        <span className="W-trans-post-date">24 march 2022, 10:30 am</span>
                                                    </div>
                                                    <div className="d-flex justify-content-end mt-4">
                                                        <Pagination className="custom-pageitem">
                                                            <Pagination.Prev />
                                                            <Pagination.Item active>{1}</Pagination.Item>
                                                            <Pagination.Item>{2}</Pagination.Item>
                                                            <Pagination.Item>{3}</Pagination.Item>
                                                            <Pagination.Ellipsis />
                                                            <Pagination.Item>{10}</Pagination.Item>
                                                            <Pagination.Next />
                                                        </Pagination>
                                                    </div>
                                                </div>
                                            </Col>
                                        </Row>
                                    </div>
                                </Tab>
                            </Tabs>
                            </div>
                        </div>
                    </Col>
                </Row>
            </div>
            <Footer />
            {/* Add Post Modal */}
            <Modal show={showModal} onHide={handleClose} className="custom-modal">
                <Modal.Header closeButton className="custom-modal-header">
                <Modal.Title className="custom-modal-header-title">Create Post</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="modal-card">
                        <Card className="modal-card-main">
                            <div className="d-flex align-items-center mb-4 mt-4">
                                <figure className="user-image">{/*<img src={UserImage} alt="" />*/}</figure>
                                <div className="User-title">
                                    <h6>Johnny Depp</h6>
                                </div>
                            </div>
                            <div className="modal-card-main-image"><Card.Img variant="top" src={ProductImage} /></div>
                            <Card.Body>
                                <Card.Title>Gucci oversized logo T-shirt</Card.Title>
                                <Card.Text>
                                Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod.
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                <Button variant="primary" className="btn-base" onClick={handleClose}>
                    post
                </Button>
                </Modal.Footer>
            </Modal>
            {/* Charity Modal*/}
            <Modal show={charityModal} onHide={charityClose} className="custom-modal">
                    <Modal.Header closeButton className="custom-modal-header">
                    <Modal.Title className="custom-modal-header-title">Propose charity</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="bid-modal">
                            <div className="modal-form">
                                <Form.Group className="mb-4 field-group" controlId="">
                                    <Form.Label>Name of proposed charity</Form.Label>
                                    <div className="enter-bid">
                                        <Form.Control
                                        className={''}
                                        type={'text'}
                                        placeholder="Jynie Foundation"
                                        />
                                        <span className="errorText">Invalid Error</span>
                                    </div>
                                </Form.Group>
                                <Form.Group className="mb-4 field-group" controlId="">
                                    <Form.Label>Email (Optional)</Form.Label>
                                    <div className="enter-bid">
                                        <Form.Control
                                        className={''}
                                        type={'text'}
                                        placeholder="example@gmail.com"
                                        />
                                        <span className="errorText">Invalid Error</span>
                                    </div>
                                </Form.Group>
                                <Form.Group className="mb-5 field-group" controlId="">
                                    <Form.Label>Website (Optional)</Form.Label>
                                    <div className="enter-bid">
                                        <Form.Control
                                        className={''}
                                        type={'text'}
                                        placeholder="example.in"
                                        />
                                        <span className="errorText">Invalid Error</span>
                                    </div>
                                </Form.Group>
                                <Row>
                                    <Col>
                                        <p>Subject to approval by Jynie foundation</p>
                                    </Col>
                                </Row>
                            </div>
                        </div>
                    </Modal.Body>
                    <Modal.Footer className="pt-0">
                    <Button variant="primary" className="btn-base" onClick={charityClose}>
                        Submit
                    </Button>
                    </Modal.Footer>
            </Modal>

            {/* Edit profile Modal */}
            <Modal show={mprofileModal} onHide={mprofileClose} className="custom-modal">
                <Modal.Header closeButton className="custom-modal-header">
                <Modal.Title className="custom-modal-header-title">Edit Profile</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="bid-modal">
                        <div class="profile-image-round">
                            <img src={OtherProfile} alt="" />
                            <span className="edit-profile-icon"></span>
                        </div>
                        <div className="modal-form">
                            <Form.Group className="mb-4 field-group" controlId="">
                                <Form.Label>Username</Form.Label>
                                <div className="enter-bid">
                                    <Form.Control
                                    className={''}
                                    type={'text'}
                                    placeholder="Johnny Depp"
                                    />
                                    <span className="errorText">Invalid Error</span>
                                </div>
                            </Form.Group>
                            <Form.Group className="mb-4 field-group" controlId="">
                                <Form.Label>Email address</Form.Label>
                                <div className="enter-bid">
                                    <Form.Control
                                    className={''}
                                    type={'text'}
                                    placeholder="jonnydep@gmail.com"
                                    />
                                    <span className="errorText">Invalid Error</span>
                                </div>
                            </Form.Group>
                            <Form.Group className="mb-5 field-group" controlId="">
                                <Form.Label>Bio</Form.Label>
                                <div className="add-product-form-field">
                                    <Form.Control as="textarea" rows={5} />
                                    <span className="errorText">Invalid Error</span>
                                    <p>Max 500 Characters</p>
                                </div>
                            </Form.Group>
                        </div>
                    </div>
                </Modal.Body>
                <Modal.Footer className="pt-0">
                <Button variant="primary" className="btn-base" onClick={mprofileClose}>
                    Save
                </Button>
                </Modal.Footer>
            </Modal>

            {/* Change Password Modal*/}
            <Modal show={changePasswordModal} onHide={mpasswordClose} className="custom-modal">
                    <Modal.Header closeButton className="custom-modal-header">
                    <Modal.Title className="custom-modal-header-title">Change Password</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="bid-modal">
                            <div className="modal-form">
                                <Form.Group className="mb-3 field-group" controlId="">
                                    <Form.Label>Current Password</Form.Label>
                                    <div className="password">
                                        <Form.Control
                                        className={''}
                                        type={'password'}
                                        placeholder=""
                                        required
                                        />
                                        <span
                                        className={'password-view'}></span>
                                    </div>
                                </Form.Group>
                                <Form.Group className="mb-3 field-group" controlId="">
                                    <Form.Label>New Password</Form.Label>
                                    <div className="password">
                                        <Form.Control
                                        className={''}
                                        type={'password'}
                                        placeholder=""
                                        required
                                        />
                                        <span
                                        className={'password-view show'}></span>
                                    </div>
                                </Form.Group>
                                <Form.Group className="mb-3 field-group" controlId="">
                                    <Form.Label>Confirm New Password</Form.Label>
                                    <div className="password">
                                        <Form.Control
                                        className={''}
                                        type={'password'}
                                        placeholder=""
                                        required
                                        />
                                        <span
                                        className={'password-view show'}></span>
                                    </div>
                                </Form.Group>
                            </div>
                        </div>
                    </Modal.Body>
                    <Modal.Footer className="pt-0">
                    <Button variant="primary" className="btn-base" onClick={mpasswordClose}>
                        Change password
                    </Button>
                    </Modal.Footer>
            </Modal>

            {/* Withdraw Modal*/}
            <Modal show={withdrawdModal} onHide={withdrawClose} className="custom-modal">
                    <Modal.Header closeButton className="custom-modal-header">
                    <Modal.Title className="custom-modal-header-title">Withdraw</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="bid-modal">
                            <div className="modal-form">
                                <Form.Group className="mb-3 field-group" controlId="">
                                    <Form.Label>Enter Amount</Form.Label>
                                    <div className="amount">
                                        <Form.Control
                                        className={''}
                                        type={'text'}
                                        placeholder=""
                                        required
                                        />
                                        <span className="errorText">Invalid Error</span>
                                    </div>
                                </Form.Group>
                            </div>
                        </div>
                    </Modal.Body>
                    <Modal.Footer className="pt-5">
                    <Button variant="primary" className="btn-base" onClick={withdrawClose}>
                        Withdraw
                    </Button>
                    </Modal.Footer>
            </Modal>
        </div>
    );
};

export default OtherProfileNewsfeed;
