import React, { useState, useEffect } from "react";
import { Tabs, Tab, Col, Row } from 'react-bootstrap';
import InfiniteScroll from 'react-infinite-scroll-component';
import { Facebook as SkeletonLoader} from 'react-content-loader';

import { Link, useLocation, useNavigate } from 'react-router-dom'
import Swal from 'sweetalert2';


import logo from "../../assets/images/logo.png"
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import Newsfeed from '../../components/Newsfeed/Newsfeed';
import Product from '../../components/ProductImage/ProductImage';
import Loader from "../../components/Loader/Loader";

// Auth-Service
import { removeUserSession, getToken } from "../../utils/AuthService";

// API-Services
import { userProfileService } from "../../service/user.service";
import { newsFeedListOfOTherUserService } from "../../service/newsFeed.service";
import { allProductOfTheUserService } from "../../service/product.service";

import OtherProfile from "../../assets/images/profile.png";

import './OtherProfileNewsfeedModule.scss';

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER_IMAGE

const OtherProfileNewsfeed = () => {

    const token = getToken();
    const navigate = useNavigate();
    const location = useLocation()
    const { userID } = location.state;

    // News-feed-state
    const [newsFeedList, setNewsFeedList] = useState([]);
    const [isHasmore, setIsHasmore] = useState(true);
    const [counter, setCounter] = useState(1);

    // Products-feed-state
    const [productList, setProductList] = useState([]);
    const [productCounter, setProductCounter] = useState(1);
    const [isProductHasmore, setIsProductHasmore] = useState(true);

    const [loading, setLoading] = useState(true);
    const [userData, setUserData] = useState(null);

    const handleFetchUserProfileData = async() => {
        const  result = await userProfileService({ token,  id: userID });
        if (result?.data?.success) {
            setUserData(result?.data?.data);
            setLoading(false)
        } else {
            setLoading(false)
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    const handleFetchNewsFeed = async(page, id) => {
        const  result = await newsFeedListOfOTherUserService({ token, perPage: 3, page, id});
        if (result?.data?.success) {
            if (newsFeedList?.length >= result?.data?.data?.newsfeed?.total) {
                setIsHasmore(false);
                return;
            }
            setNewsFeedList(newsFeedList?.concat(result?.data?.data?.newsfeed?.data));
            setCounter(counter+1);
        } else {
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }


    const handleGetProduct = async(page, id) => {

        const result = await allProductOfTheUserService({ token, perPage: 8, page, id })
        if (result?.data?.success) {
            if (productList?.length >= result?.data?.data?.products?.total) {
                setIsProductHasmore(false);
                return;
            }
            setProductList(productList?.concat(result?.data?.data?.products?.data));
            setProductCounter(productCounter+1);
        } else {
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }


    useEffect(() => {
        if(userID){
            handleFetchUserProfileData();
            if (newsFeedList?.length === 0) {
                handleFetchNewsFeed(1, userID);
            }
            if (productList?.length === 0) {
                handleGetProduct(1, userID);
            }
        }
        
        const timeoutId = setTimeout(() => 3000);
        return function cleanup() {
            clearTimeout(timeoutId);
        }
    }, [userID])


    return (
        <div className="App">
            <Header siteLogo={logo} />
            <div className="container">
                {loading ? <Loader/> :
                    <Row className="mb-5 main-top-padding">
                        <Col lg={{ span: 10, }} md={{ span: 8, }} xs={{ span: 12, }} className="mx-auto reset-padding-mobile">
                            <div className="otherProfilefeed">
                                <div className="ou-profile">
                                    <div className="ou-profile-cover"></div>
                                    <div className="ou-profile-cont">
                                        <div className="ou-profile-cont-info">
                                            <div className="ou-profile-cont-info-image">
                                                <img src={userData?.user?.image_url ? `${url}${userData?.user?.image_url}` : OtherProfile} alt="" />
                                            </div>
                                            <div className="ou-profile-cont-info-cont">
                                                <h2 className="ou-profile-cont-info-cont-name">{userData?.user?.username}</h2>
                                                <span className="ou-profile-cont-info-cont-value">{userData?.product_count} Products</span>
                                            </div>
                                        </div>
                                        <div className="ou-profile-cont-desc">
                                            <p className="ou-profile-cont-desc-text">
                                                {userData?.bio}
                                            </p>
                                        </div>
                                    </div>
                                </div>

                                <div className="ouProfile-tab">
                                <Tabs defaultActiveKey="newsfeed" id="otheruserprofile-newsfeed" className="ouProfile-tab-group">
                                    <Tab eventKey="newsfeed" title="Newsfeed" className="ouProfile-tab-group-tab">
                                        <Row className="mx-0">
                                            <Col lg={{ span: 9, }} xs={{ span: 12, }} className="mx-auto pb-5 reset-padding-mobile">
                                                <InfiniteScroll
                                                    dataLength={newsFeedList.length}
                                                    next={() => handleFetchNewsFeed(counter, userID)}
                                                    hasMore={isHasmore}
                                                    loader={
                                                        <div>
                                                            <SkeletonLoader/>
                                                            <SkeletonLoader/>
                                                        </div>
                                                    }
                                                    endMessage={
                                                        <p style={{ textAlign: 'center' }}>
                                                            <b>Yay! You have seen it all</b>
                                                        </p>
                                                    }
                                                >
                                                    {newsFeedList?.map((feedItem) => <Newsfeed key={feedItem.id} feedItem={feedItem} /> )}
                                                </InfiniteScroll>
                                            </Col>
                                        </Row>
                                    </Tab>
                                    <Tab eventKey="products" title="Products" className="ouProfile-tab-group-tab">
                                        <div className="px-4 reset-col-padding pb-5">
                                                <InfiniteScroll
                                                    dataLength={productList.length}
                                                    next={() => handleGetProduct(productCounter, userID)}
                                                    hasMore={isProductHasmore}
                                                    loader={
                                                        <div>
                                                            <SkeletonLoader/>
                                                        </div>
                                                    }
                                                    endMessage={
                                                        <p style={{ textAlign: 'center' }}>
                                                            <b>Yay! You have seen it all</b>
                                                        </p>
                                                    }
                                                >
                                                    <Row>
                                                        {productList?.map(productItem => 
                                                            <Col key={productItem.id} md={{ span: 6, }} xs={{ span: 6, }} lg={{ span: 4,}}>
                                                                <Product product={productItem} />
                                                            </Col>
                                                        )}
                                                    </Row>
                                                </InfiniteScroll>
                                        </div>
                                    </Tab>
                                </Tabs>
                                </div>
                            </div>
                            
                            
                        </Col>
                    </Row>
                }
            </div>
            <Footer />
        </div>
    );
};

export default OtherProfileNewsfeed;
