import React, { useState, useEffect } from "react";
import { Tabs, Tab, Col, Row, Button, Modal, Card } from 'react-bootstrap';

import { useNavigate } from 'react-router-dom'
import Swal from 'sweetalert2';


import logo from "../../assets/images/logo.png"
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import Newsfeed from '../../components/Newsfeed/Newsfeed';
import Product from '../../components/ProductImage/ProductImage';
import Loader from "../../components/Loader/Loader";

// Auth-Service
import { removeUserSession, getToken } from "../../utils/AuthService";


// API-Services
import { getUserProfileService, sellerRequestService } from "../../service/profile.service";


import ProductImage from '../../assets/images/gucci-black-tshirt.jpg';

import OtherProfile from "../../assets/images/profile.png";

import './MyProfileNewsfeedModule.scss';

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER_IMAGE

const OtherProfileNewsfeed = () => {

    const token = getToken();
    const navigate = useNavigate();

    const [showModal, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    // User-state
    const [loading, setLoading] = useState(true);
    const [userData, setUserData] = useState(null);

    // Saller-request-state
    const [isSellerRequestPreApply, setIsSellerRequestPreApply] = useState(true);
    const [isSellerRequestPending, setIsSellerRequestPending] = useState(false);
    const [isSellerRequestApproved, setIsSellerRequestApproved] = useState(false);
    

    const handleFetchUserProfileData = async() => {
        const  result = await getUserProfileService({ token });
        if (result?.data?.success) {
            setUserData(result?.data?.data);
            if(result?.data?.data?.user?.seller_request){
                setIsSellerRequestPreApply(false);
                if(result?.data?.data?.user?.seller_request?.is_approved){
                    setIsSellerRequestPending(false);
                    setIsSellerRequestApproved(true);
                }else{
                    setIsSellerRequestPending(true);
                }
                
            }
            setLoading(false)
        } else {
            setLoading(false)
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    const handleSellerRequest = async() => {
        const  result = await sellerRequestService({ token });
        if (result?.data?.success) {
            setIsSellerRequestPreApply(false);
            setIsSellerRequestPending(true);
        } else {
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    useEffect(() => {
        if(!userData){
            handleFetchUserProfileData();
        }
        
        const timeoutId = setTimeout(() => 3000);
        return function cleanup() {
            clearTimeout(timeoutId);
        }
    }, [])

    return (
        <div className="App">
            <Header siteLogo={logo} />
            {loading ? <Loader/> :
            <div className="container">
                <Row className="mb-5 main-top-padding">
                    <Col lg={{ span: 10, }} md={{ span: 8, }} xs={{ span: 12, }} className="mx-auto reset-padding-mobile">
                        <div className="otherProfilefeed">
                            <div className="ou-profile">
                                <div className="ou-profile-cover">
                                <img src={userData?.user?.cover_image_url ? `${url}${userData?.user?.cover_image_url}` : OtherProfile} alt="" />
                                    <div className="edit-cover">
                                        <span className="edit-cover-text">Edit Cover Photo</span>
                                    </div>
                                </div>
                                <div className="ou-profile-cont">
                                    <div className="ou-profile-cont-info">
                                        <div className="ou-profile-cont-info-image">
                                            <img src={userData?.user?.image_url ? `${url}${userData?.user?.image_url}` : OtherProfile} alt="" />
                                        </div>
                                        <div className="ou-profile-cont-info-cont">
                                            <h2 className="ou-profile-cont-info-cont-name">{userData?.user?.username}</h2>
                                            <span className="ou-profile-cont-info-cont-value">{userData?.product_count} Products</span>
                                        </div>
                                    </div>
                                    <div className="ou-profile-cont-withtext">
                                        <div className="ou-profile-cont-desc">
                                            <p className="ou-profile-cont-desc-text">{userData?.user?.bio}</p>
                                        </div>
                                        {isSellerRequestPreApply &&
                                        <div className="seller-status">
                                            <span onClick={handleSellerRequest} className="seller-status-active btn-base">Activate seller status</span>
                                            <span className="seller-status-overflowBtn"></span>
                                        </div>}
                                        {isSellerRequestPending &&
                                        <div className="approval-status">
                                            <span className="approval-status-active btn-base">Pending Approval</span>
                                            <span className="approval-status-overflowBtn"></span>
                                        </div>}
                                    </div>
                                </div>
                            </div>

                            <div className="myProfile-tab">
                            <Tabs defaultActiveKey="newsfeed" id="otheruserprofile-newsfeed" className="myProfile-tab-group">
                                <Tab eventKey="newsfeed" title="Newsfeed" className="myProfile-tab-group-tab">
                                    <Row className="mx-0">
                                        <Col lg={{ span: 9, }} xs={{ span: 12, }} className="mx-auto pb-5 reset-padding-mobile">
                                            <div className="btn-base c-newPost for-desktop">
                                                <span className="c-newPost-text" onClick={handleShow}>Create New Post</span>
                                            </div>
                                            <div className="btn-base c-newPost for-mobile">
                                                <span className="c-newPost-text" onClick={handleShow}>Post</span>
                                            </div>
                                            <Newsfeed />
                                            <Newsfeed /> 
                                            <Newsfeed /> 
                                            <Newsfeed /> 
                                        </Col>
                                    </Row>
                                </Tab>
                                <Tab eventKey="products" title="Products" className="myProfile-tab-group-tab">
                                    <div className="px-4 no-products pb-5">
                                        <Row className="justify-content-center">
                                            <Col lg={{ span: 9, }} xs={{ span: 12, }} className="text-center">
                                                <h6 className="no-products-title">There are no products</h6>
                                                <p className="no-products-text">To add a product Please activate seller</p>
                                            </Col>
                                        </Row>
                                        <Row className="justify-content-center">
                                            <Col lg={{ span: 3, }} xs={{ span: 12, }}>
                                                <Button className="btn-base no-products-btn">Activate seller status</Button>
                                            </Col>
                                        </Row>
                                    </div>
                                </Tab>
                                <Tab eventKey="order" title="Order" className="myProfile-tab-group-tab">
                                    <div className="px-4 no-products pb-5 mb-5">
                                        <Row className="justify-content-center">
                                            <Col lg={{ span: 9, }} xs={{ span: 12, }} className="text-center">
                                                <h6 className="no-products-title">Seller activation pending approval</h6>
                                            </Col>
                                        </Row>
                                    </div>
                                </Tab>
                                <Tab eventKey="purchasehistory" title="Purchase History" className="myProfile-tab-group-tab">
                                    <Row>
                                        <Col lg={{ span: 9, }} xs={{ span: 12, }} className="mx-auto pb-5">
                                            <Newsfeed />
                                            <Newsfeed /> 
                                            <Newsfeed /> 
                                            <Newsfeed /> 
                                        </Col>
                                    </Row>
                                </Tab>
                                <Tab eventKey="wallet" title="Wallet" className="myProfile-tab-group-tab">
                                    <Row>
                                        <Col lg={{ span: 9, }} xs={{ span: 12, }} className="mx-auto pb-5">
                                            <Newsfeed />
                                            <Newsfeed /> 
                                            <Newsfeed /> 
                                            <Newsfeed /> 
                                        </Col>
                                    </Row>
                                </Tab>
                            </Tabs>
                            </div>
                        </div>
                    </Col>
                </Row>
            </div>
            }
            <Footer />
            <Modal show={showModal} onHide={handleClose} className="custom-modal">
                <Modal.Header closeButton className="custom-modal-header">
                <Modal.Title className="custom-modal-header-title">Create Post</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="modal-card">
                        <Card className="modal-card-main">
                            <div className="d-flex align-items-center mb-4 mt-4">
                                <figure className="user-image">{/*<img src={UserImage} alt="" />*/}</figure>
                                <div className="User-title">
                                    <h6>Johnny Depp</h6>
                                </div>
                            </div>
                            <div className="modal-card-main-image"><Card.Img variant="top" src={ProductImage} /></div>
                            <Card.Body>
                                <Card.Title>Gucci oversized logo T-shirt</Card.Title>
                                <Card.Text>
                                Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod.
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                <Button variant="primary" className="btn-base" onClick={handleClose}>
                    post
                </Button>
                </Modal.Footer>
            </Modal>
        </div>
    );
};

export default OtherProfileNewsfeed;
