import React from 'react';
import { BrowserRouter, Route, Routes } from "react-router-dom";
import './App.scss';

import Packages from './pages/Packages/Packages';
import HomeNewsfeed from './pages/HomeNewsfeed/HomeNewsfeed'
/*import Error404 from './pages/Error404/Error404';*/
import Signup from './pages/SignUp/SignUp';
import ForgotPassword from './pages/ForgotPassword/ForgotPassword';
import Verification from './pages/Verification/Verification';
import ResetPassword from './pages/ResetPassword/ResetPassword';

// Auth-Service
import PrivateRoute from './utils/PrivateRoute';
import Singlecategory from './pages/Singlecategory/Singlecategory';
import Store from './pages/Store/Store';

//Static pages import here for checking HTML
import Searchresult from './pages/Searchresult/Searchresult';
import Singleproduct from './pages/Singleproduct/Singleproduct';
import SingleProductAuction from './pages/SingleProductAuction/SingleProductAuction';
import OtherProfileNewsfeed from './pages/OtherProfileNewsfeed/OtherProfileNewsfeed';
import NewsfeedSelectproduct from './pages/NewsfeedSelectproduct/NewsfeedSelectproduct';
import MyProfileNewsfeed from './pages/MyProfileNewsfeed/MyProfileNewsfeed';
import AddProduct from './pages/AddProduct/AddProduct';
import AddauctioProduct from './pages/AddAuctionProduct/AddAuctionProduct';
import MyprofileProductsVerify from './pages/MyprofileProductsVerifySeller/MyprofileProductsVerifySeller';
import AddAddress from './pages/AddAddress/AddAddress';
import BankAccount from './pages/BankAccount/BankAccount';
import Cart from './pages/Cart/Cart';
import Checkout from './pages/Checkout/Checkout';




const App = () => (
  <BrowserRouter>
  <Routes>
      <Route path="/login" element={<Signup />} />
      <Route path="/*" element={<Signup />} />

      {/* Start----- Static pages import here for checking HTML */}
      <Route path="/singlecategory" element={<Singlecategory />} />
      <Route path="/searchresult" element={<Searchresult />} />
      <Route path="/singleproduct" element={<Singleproduct />} />
      <Route path="/singleproductauction" element={<SingleProductAuction />} />
      <Route path="/otherprofilenewsfeed" element={<OtherProfileNewsfeed />} />
      <Route path="/newsfeedselectproduct" element={<NewsfeedSelectproduct />} />
      <Route path="/myprofilenewsfeed" element={<MyProfileNewsfeed />} />
      <Route path="/myprofileproductsverify" element={<MyprofileProductsVerify />} />
      <Route path="/addproduct" element={<AddProduct />} />
      <Route path="/addauctioproduct" element={<AddauctioProduct />} />
      <Route path="/addaddress" element={<AddAddress />} />
      <Route path="/bankaccount" element={<BankAccount />} />
      <Route path="/cart" element={<Cart />} />
      <Route path="/checkout" element={<Checkout />} />
      {/* Static pages import here for checking HTML ----End */}
      
      
      <Route path="/forget-password" element={<ForgotPassword />} />
      <Route path="/otp-verify" element={<Verification />} />
      <Route path="/reset-password" element={<ResetPassword />} />

      <Route element={<PrivateRoute/>}>
        <Route path="/" element={<HomeNewsfeed/>} />
      </Route>
      <Route element={<PrivateRoute/>}>
        <Route path="/feed" element={<HomeNewsfeed/>} />
      </Route>
      <Route element={<PrivateRoute/>}>
        <Route path="/packages" element={<Packages/>} />
      </Route>
      <Route element={<PrivateRoute/>}>
        <Route path="/Store" element={<Store/>} />
      </Route>
      <Route element={<PrivateRoute/>}>
        <Route path="/single-category" element={<Singlecategory/>} />
      </Route>
      <Route element={<PrivateRoute/>}>
        <Route path="/profile" element={<MyProfileNewsfeed/>} />
      </Route>
      <Route element={<PrivateRoute/>}>
        <Route path="/user-profile" element={<OtherProfileNewsfeed />} />
      </Route>
      <Route element={<PrivateRoute/>}>
        <Route path="/search-result" element={<Searchresult />} />
      </Route>
      <Route element={<PrivateRoute/>}>
        <Route path="/single-product" element={<Singleproduct />} />
      </Route>
      <Route element={<PrivateRoute/>}>
        <Route path="/single-product-bid" element={<SingleProductAuction />} />
      </Route>
      
  </Routes>
  </BrowserRouter>
)

export default App;
