import React, { useState } from "react";
import { Button, Col, Form, Row } from 'react-bootstrap';
import { useNavigate,  useLocation } from 'react-router-dom';
import Swal from 'sweetalert2';

// API-Service
import { resetPasswordService } from "../../service/auth.service";

const ResetPassword  = () => {
    const navigate = useNavigate();
    const { state } = useLocation();
    const [notValidated, setNotValidated] = useState(false);
    const [showPassword, setShowPassword] = useState(false);
    const [showPasswordTwo, setShowPasswordTwo] = useState(false);
    const [passwordNotMatch, setPasswordNotMatch] = useState(false);

    const [formData, setFormData] = useState({
        password: '',
        password_confirmation: '',
    });

     /**
     * @function handleSubmit
     * @param event
     * @description This function is used to reset password
     */

      const handleSubmit =  async (event) => {
        event.preventDefault();
        const form = event.currentTarget;
        if (form.checkValidity() === false || passwordNotMatch){
            setNotValidated(true);
        }else{
            setNotValidated(false);
            const result = await resetPasswordService({
                email: state?.email,
                code: state?.code,
                password: formData.password,
                password_confirmation: formData.password_confirmation
            })

            if(result?.data?.success){
                navigate("/login")
            }else{
                Swal.fire({
                    title: 'Error!',
                    text: result?.response?.data?.message || "Something went wrong",
                    icon: 'error',
                    confirmButtonText: 'Try Again',
                    confirmButtonColor: "#242c44"
                })
                navigate("/otp-verify")
            }
        }
      };

    const handlePassword = (e) => {
        setFormData({
            ...formData,
            password: e.target.value
        })
    }
  
    const handleConfirmPassword = (e) => {
          if(formData.password === e.target.value){
              setFormData({
                  ...formData,
                  password_confirmation: e.target.value
              })
              setPasswordNotMatch(false)
          }else{
              setPasswordNotMatch(true)

          }
    }

    const handleShowPasswordOne = () => {
        setShowPassword(!showPassword)
    }

    const handleShowPasswordTwo = () => {
        setShowPasswordTwo(!showPasswordTwo)
    }

    const handleError = () => {
        setNotValidated(false);
    }

        return (
            <div className="container">
                <Row className="mb-5 main-top-padding">
                    <Col md={{ span: 5, }} className="text-center mx-auto">{
                        <h2 className="pageTitle">Reset Password?</h2>
                    }</Col>
                </Row>
                <Row>
                    <Col md={{ span: 8, }} lg={{ span: 5, }} className="mx-auto">{
                        <Form noValidate onSubmit={handleSubmit}>
                            <Form.Group className="mb-3 field-group" controlId="">
                                <Form.Label>New Password</Form.Label>
                                    <div className="password">
                                        <Form.Control className={notValidated ? "error" : ''} type={showPassword ? "text": "password"} placeholder="Password" onChange={handleError} onBlur={handlePassword} required />
                                        <span onClick={handleShowPasswordOne} className={showPassword ? "password-view show": "password-view"}></span>
                                    </div>
                                    {notValidated && <span className="errorText show">Must be 8 or more characters</span>}
                            </Form.Group>

                            <Form.Group className="mb-3 field-group" controlId="">
                                <Form.Label>Confirm Password</Form.Label>
                                    <div className="password">
                                        <Form.Control className={notValidated ? "error" : ''} type={showPasswordTwo ? "text": "password"}  placeholder="Password" onChange={handleError} onBlur={handleConfirmPassword} required/>
                                        <span onClick={handleShowPasswordTwo} className={showPasswordTwo ? "password-view show": "password-view"}></span>
                                    </div>
                                    {passwordNotMatch && <span className="errorText show">Please make sure your passwords match.</span>}
                            </Form.Group>

                            <Button variant="primary" className="btn-base" type="submit">
                                Reset password
                            </Button>
                        </Form>
                    }</Col>
                </Row>
            </div>
        );
    }

export default ResetPassword;
