import axios from "axios"

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER

/**
* @function allBrandListService
* @param {token}
* @description This function is used to get all brands
*/

export const allBrandListService = async({ token }) =>{
    try {
        const result = await axios.get(`${url}/profile/product/get_all_brands`,{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}

/**
* @function singleBrandListService
* @param {token, id}
* @description This function is used to get single brand
*/

export const singleBrandListService = async({ token, id }) =>{
    try {
        const result = await axios.get(`${url}/profile/product/get_all_brands?product_category_id=${id}`,{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}


