import React, {} from "react";
import { Col, Container, Row, Dropdown, Button, Form } from 'react-bootstrap';
import logo from "../../assets/images/logo.png";
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import EditIcon from '../../assets/images/icons/edit.svg';
import FlashIcon from '../../assets/images/icons/flash.svg';
import TrashIcon from '../../assets/images/icons/trash-can.svg';

import './AddAddressModule.scss';

const AddAddress = () => {

    return (
        <div className="App">
        <Header siteLogo={logo} />
            <Container>
                <div className="addressPage">
                    <Row className="main-top-padding">
                        <Col md={{ span: 12, }} className="text-center-desktop mx-auto">
                            <h2 className="pageTitle">My Addresses</h2>
                        </Col>
                    </Row>
                    <Row className="mb-5">
                        <Col xs={{ span: 12, }} className="mobile-pad-reset">
                            <div className="border-wrap">
                                <span className="address-btn">Add a New Address</span>
                            </div>
                            <div className="newAddress-form">
                                <h6 className="newAddress-form-title">Add a New Address</h6>
                                <div className="newAddress-form-row">
                                    <div className="newAddress-form-row-col">
                                    <Form.Group className="field-group" controlId="">
                                        <Form.Label>Name</Form.Label>
                                        <div className="add-product-form-field">
                                            <Form.Control
                                            className={''}
                                            type={'text'}
                                            placeholder="Johnny Depp"
                                            />
                                        </div>
                                    </Form.Group>
                                    </div>
                                    <div className="newAddress-form-row-col">
                                    <Form.Group className="field-group" controlId="">
                                        <Form.Label>Mobile Number</Form.Label>
                                        <div className="add-product-form-field">
                                            <Form.Control
                                            className={''}
                                            type={'text'}
                                            placeholder="+60 9876543210"
                                            />
                                        </div>
                                    </Form.Group>
                                    </div>
                                </div>

                                <div className="newAddress-form-row">
                                    <div className="newAddress-form-row-col">
                                    <Form.Group className="field-group" controlId="">
                                        <Form.Label>Address Line 1</Form.Label>
                                        <div className="add-product-form-field">
                                            <Form.Control
                                            className={''}
                                            type={'text'}
                                            placeholder=""
                                            />
                                        </div>
                                    </Form.Group>
                                    </div>
                                    <div className="newAddress-form-row-col">
                                    <Form.Group className="field-group" controlId="">
                                        <Form.Label>Address Line 2</Form.Label>
                                        <div className="add-product-form-field">
                                            <Form.Control
                                            className={''}
                                            type={'text'}
                                            placeholder=""
                                            />
                                        </div>
                                    </Form.Group>
                                    </div>
                                </div>
                                <div className="newAddress-form-row">
                                    <div className="newAddress-form-row-col">
                                    <Form.Group className="field-group" controlId="">
                                        <Form.Label>Postcode</Form.Label>
                                        <div className="add-product-form-field">
                                            <Form.Control
                                            className={''}
                                            type={'text'}
                                            placeholder="41200"
                                            />
                                        </div>
                                    </Form.Group>
                                    </div>
                                </div>
                                <div className="newAddress-form-row">
                                    <div className="newAddress-form-row-col">
                                    <Form.Group className="field-group" controlId="">
                                        <Form.Label>City/Town</Form.Label>
                                        <div className="add-product-form-field">
                                            <Form.Control
                                            className={''}
                                            type={'text'}
                                            placeholder=""
                                            />
                                        </div>
                                    </Form.Group>
                                    </div>
                                    <div className="newAddress-form-row-col">
                                    <Form.Group className="field-group" controlId="">
                                        <Form.Label>State</Form.Label>
                                        <div className="add-product-form-field">
                                            <Form.Select className={'form-select'}>
                                            <option>State 1</option>
                                            <option>State 2</option>
                                            <option>State 3</option>
                                            <option>State 4</option>
                                            </Form.Select>
                                        </div>
                                    </Form.Group>
                                    </div>
                                </div>
                                <div className="newAddress-form-row">
                                    <div className="newAddress-form-row-col">
                                    <Form.Group className="field-group" controlId="">
                                        <Form.Label>Country</Form.Label>
                                        <div className="add-product-form-field select-group">
                                            <Form.Select className={'form-select'}>
                                            <option>Country 1</option>
                                            <option>Country 2</option>
                                            <option>Country 3</option>
                                            <option>Country 4</option>
                                            </Form.Select>
                                        </div>
                                    </Form.Group>
                                    </div>
                                </div>
                                <Row className="align-items-center mt-5 mobile-align-end">
                                    <Col xs={{ span: 7, }} md={{ span: 3, }}>
                                        <Button className="btn-base">Save</Button>
                                    </Col>
                                    <Col xs={{ span: 4, }} md={{ span: 4, }}>
                                        <span className="cancel-text">Cancel</span>
                                    </Col>
                                </Row>
                            </div>
                        </Col>
                    </Row>
                    <Row className="mb-3">
                        <Col xs={{ span: 12, }} className="mobile-pad-reset">
                            <div className="border-wrap active">
                                <div className="address-block">
                                    <div className="address-block-info">
                                        <h5 className="address-block-info-title">Johnny Depp</h5>
                                        <span className="address-block-info-phone">+60 9876543210</span>
                                        <span className="address-block-info-primary">Primary Address</span>
                                    </div>
                                    <p className="address-block-text">Lorong Ladang,41200, Klang, Selangor,  Malaysia 41200</p>
                                </div>
                                <Dropdown align="end" className="custom-dropdown">
                                    <Dropdown.Toggle id="dropdown-autoclose-true" className="seller-status-overflowBtn">
                                    </Dropdown.Toggle>

                                    <Dropdown.Menu className="custom-dropdown-menu">
                                    <Dropdown.Item href="#">
                                        <span className="custom-dropdown-menu-row">
                                            <span className="custom-dropdown-menu-row-icon">
                                                <img src={FlashIcon} alt="" />
                                            </span>
                                            <span className="custom-dropdown-menu-row-title">Set as primary</span>
                                        </span>
                                    </Dropdown.Item>
                                    <Dropdown.Item href="#">
                                        <span className="custom-dropdown-menu-row">
                                            <span className="custom-dropdown-menu-row-icon">
                                                <img src={EditIcon} alt="" />
                                            </span>
                                            <span className="custom-dropdown-menu-row-title">Edit</span>
                                        </span>
                                    </Dropdown.Item>
                                    <Dropdown.Item href="#">
                                        <span className="custom-dropdown-menu-row">
                                            <span className="custom-dropdown-menu-row-icon">
                                                <img src={TrashIcon} alt="" />
                                            </span>
                                            <span className="custom-dropdown-menu-row-title">Delete</span>
                                        </span>
                                    </Dropdown.Item>
                                    </Dropdown.Menu>
                                </Dropdown>
                            </div>
                        </Col>
                    </Row>
                    <Row className="mb-3">
                        <Col xs={{ span: 12, }} className="mobile-pad-reset">
                            <div className="border-wrap">
                                <div className="address-block">
                                    <div className="address-block-info">
                                        <h5 className="address-block-info-title">Johnny Depp</h5>
                                        <span className="address-block-info-phone">+60 9876543210</span>
                                    </div>
                                    <p className="address-block-text">Lorong Ladang,41200, Klang, Selangor,  Malaysia 41200</p>
                                </div>
                                <Dropdown align="end" className="custom-dropdown">
                                    <Dropdown.Toggle id="dropdown-autoclose-true" className="seller-status-overflowBtn">
                                    </Dropdown.Toggle>

                                    <Dropdown.Menu className="custom-dropdown-menu">
                                    <Dropdown.Item href="#">
                                        <span className="custom-dropdown-menu-row">
                                            <span className="custom-dropdown-menu-row-icon">
                                                <img src={FlashIcon} alt="" />
                                            </span>
                                            <span className="custom-dropdown-menu-row-title">Set as primary</span>
                                        </span>
                                    </Dropdown.Item>
                                    <Dropdown.Item href="#">
                                        <span className="custom-dropdown-menu-row">
                                            <span className="custom-dropdown-menu-row-icon">
                                                <img src={EditIcon} alt="" />
                                            </span>
                                            <span className="custom-dropdown-menu-row-title">Edit</span>
                                        </span>
                                    </Dropdown.Item>
                                    <Dropdown.Item href="#">
                                        <span className="custom-dropdown-menu-row">
                                            <span className="custom-dropdown-menu-row-icon">
                                                <img src={TrashIcon} alt="" />
                                            </span>
                                            <span className="custom-dropdown-menu-row-title">Delete</span>
                                        </span>
                                    </Dropdown.Item>
                                    </Dropdown.Menu>
                                </Dropdown>
                            </div>
                        </Col>
                    </Row>
                    <Row className="mb-4 justify-content-end">
                        <Col md={{ span: 6, }} xs={{ span: 12, }}>
                            <div className="d-flex mt-5">
                                <Button className="btn-base">Save</Button>
                            </div>
                        </Col>
                    </Row>
                </div>
        </Container>
        <Footer />
    </div>
    );
}

export default AddAddress;
