import React, { useState } from 'react';
import { Button, Col, Form, Row } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';

// API-Services
import { loginService } from '../../service/auth.service';

// Utility Service
import { setUserSession } from '../../utils/AuthService';

import './SignInmodule.scss';

const Signin = () => {
  const navigate = useNavigate();
  const [notValidated, setNotValidated] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [formData, setFormData] = useState({
    emailOrUsername: '',
    password: '',
  });

  /**
   * @function handleSubmit
   * @param event
   * @description This function is used to signin the user
   */
  const handleSubmit = async (event) => {
    event.preventDefault();
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      setNotValidated(true);
      return;
    }
    
    const result = await loginService({
      username: formData.emailOrUsername,
      password: formData.password,
    });

    if (result?.data?.success) {
      setUserSession(result?.data?.data?.api_key, result?.data?.data?.user);
      navigate('/');
    } else {
      Swal.fire({
        title: 'Error!',
        text: result?.response?.data?.message || 'Something went wrong',
        icon: 'error',
        confirmButtonText: 'Try Again',
        confirmButtonColor: '#242c44',
      });
    }
  };

  const handleEmailOrUsername = (e) => {
    setFormData({
      ...formData,
      emailOrUsername: e.target.value,
    });
  };

  const handlePassword = (e) => {
    setFormData({
      ...formData,
      password: e.target.value,
    });
  };

  const handleShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleForgetPasswordRoute = () => {
    navigate('/forget-password');
  };

  return (
    <div>
      <Form noValidate onSubmit={handleSubmit}>
        <Form.Group className="mb-3 field-group" controlId="">
          <Form.Label>Username or email address*</Form.Label>
          <Form.Control
            className={notValidated ? 'error' : ''}
            placeholder="Enter user-name or email"
            onChange={handleEmailOrUsername}
            required 
          />
          {notValidated && (
            <span className="errorText show">Invalid Username or Email</span>
          )}
        </Form.Group>

        <Form.Group className="mb-3 field-group" controlId="">
          <Form.Label>Password*</Form.Label>
          <div className="password">
            <Form.Control
              className={notValidated ? 'error' : ''}
              type={showPassword ? 'text' : 'password'}
              placeholder="Password"
              onChange={handlePassword}
              required
            />
            <span
              onClick={handleShowPassword}
              className={
                showPassword ? 'password-view show' : 'password-view'
              }></span>
          </div>
          {notValidated && (
            <span className="errorText show">Invalid Password</span>
          )}
        </Form.Group>

        <Row className="mt-4 pt-1 mb-4 row align-items-center">
          <Col>
            <Form.Group className="" controlId="formBasicCheckbox">
              <Form.Check type="checkbox" label="Remember me" />
            </Form.Group>
          </Col>
          <Col>
            <span className="forgetLink" onClick={handleForgetPasswordRoute}>
              Forget your Password?
            </span>
          </Col>
        </Row>
        <Button variant="primary" className="btn-base" type="submit">
          Sign In
        </Button>
      </Form>
    </div>
  );
};

export default Signin;
