import React, { Component } from "react";
import { Tabs, Tab, Col, Row } from 'react-bootstrap';
import logo from "../../assets/images/logo.png"
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import Newsfeed from '../../components/Newsfeed/Newsfeed';
import Product from '../../components/ProductImage/ProductImage';

import OtherProfile from "../../assets/images/profile.png";

import './NewsfeedSelectproductModule.scss';

const NewsfeedSlectproduct = () => {
    return (
        <div className="App">
            <Header siteLogo={logo} />
            <div className="container">
                <Row className="mb-5 main-top-padding">
                    <Col lg={{ span: 10, }} xs={{ span: 12, }} className="mx-auto reset-padding-mobile">
                        <div className="singleProduct">
                            <h2 className="singleProduct-selecttitle">Select Product</h2>
                            <div className="px-4 reset-col-padding pb-5">
                                <Row>
                                    <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                        <Product />
                                    </Col>
                                    <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                        <Product />
                                    </Col>
                                    <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                        <Product />
                                    </Col>
                                    <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                        <Product />
                                    </Col>
                                    <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                        <Product />
                                    </Col>
                                    <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                        <Product />
                                    </Col>
                                    <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                        <Product />
                                    </Col>
                                    <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                        <Product />
                                    </Col>
                                    <Col lg={{ span: 4, }} xs={{ span: 4, }} className="mx-auto">
                                        <Product />
                                    </Col>
                                </Row>
                            </div>
                        </div>
                    </Col>
                </Row>
            </div>
            <Footer />
        </div>
    );
};

export default NewsfeedSlectproduct;
