import React, {} from "react";
import { Card, Dropdown} from "react-bootstrap";
import productimage from './../../assets/images/guccishirt-2nd.jpg';
import './ProducImageModule.scss';

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER_IMAGE


const Productoflist = ({product}) => {
    return (
        <div className="productImage">
            <Card className="productImage-widget">
                <div className="productImage-widget-wrap">
                    <Card.Img variant="top" className="productImage-widget-wrap-image" src={product?.image_urls?.length > 0 ? `${url}${product.image_urls[0]}` : productimage} />
                    {!product?.in_stock && <span className="productImage-widget-wrap-tag">Sold out</span> }
                    <Dropdown align="end" className="custom-dropdown">
                        <Dropdown.Toggle id="dropdown-autoclose-true" className="seller-status-overflowBtn">
                        </Dropdown.Toggle>

                        <Dropdown.Menu className="custom-dropdown-menu">
                        <Dropdown.Item href="#">
                            <span className="custom-dropdown-menu-row">
                                <span className="custom-dropdown-menu-row-title">Sold Out</span>
                            </span>
                        </Dropdown.Item>
                        <Dropdown.Item href="#">
                            <span className="custom-dropdown-menu-row">
                                <span className="custom-dropdown-menu-row-title">In Stock</span>
                            </span>
                        </Dropdown.Item>
                        <Dropdown.Item href="#">
                            <span className="custom-dropdown-menu-row">
                                <span className="custom-dropdown-menu-row-title">Archive</span>
                            </span>
                        </Dropdown.Item>
                        </Dropdown.Menu>
                    </Dropdown>
                </div>
            </Card>
        </div>
    );
};

export default Productoflist;
