import React, { useState, useEffect } from "react";
import { Col, Container, Row, Tabs, Tab, Button } from 'react-bootstrap';
import Swal from 'sweetalert2';
import { useNavigate } from 'react-router-dom';



import logo from "../../assets/images/logo.png";
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import Storehero from "../../assets/images/store-hero.png";
import DonationimgFirst from "../../assets/images/icons/members.svg";
import DonationimgSecond from "../../assets/images/icons/donations.svg";

import Categorycarousel from '../../components/Categorycarousel/Categorycarousel';
import Loader from "../../components/Loader/Loader";

import ProductofBid from '../../components/Products/ProductofBid';
import ProductofList from '../../components/Products/ProductofList';
import Brand from '../../components/Brand/Brand';


import Celebrities from './Utlity/Celebrities';

// API-Service
import { 
  
    allBrandListService,
    singleBrandListService,
    celebrityListService
} from "../../service/store.service";

import {   
    allProductCategoryService, 
    singleCategoryProductService, 
    allProductService 
} from "../../service/product.service";

// Auth-Service
import { removeUserSession, getToken  } from "../../utils/AuthService";

import "./Storemodule.scss"

const Storepage = () => {

    const [categoryList, setCategoryList] = useState([]);
    const [productList, setProductList] = useState([]);
    const [celebrityList, setCelebrityList] = useState([]);
    const [brandList, setBrandList] = useState([]);
    const [filteredBrand, setFilteredBrand] = useState([]);

    const [cateGoryloading, setCateGoryloading] = useState(true)
    const [productLoading, setProductLoading] = useState(true)
    const [brandLoading, setBrandLoading] = useState(true)
    const [filteredBrandLoading, setFilteredBrandLoading] = useState(true)
    const [celebrityLoading, setCelebrityLoading] = useState(true)

    const token = getToken();
    const navigate = useNavigate();

    const fetchProductCategoryData = async () => {
        const result = await allProductCategoryService({ token })
        if (result?.data?.success) {
            setCategoryList(result?.data?.data?.product_categories)
            setCateGoryloading(false)
        } else {
            setCateGoryloading(false)
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    const fetchBrandsData = async () => {
        const result = await allBrandListService({ token })
        if (result?.data?.success) {
            setBrandList(result?.data?.data?.brands)
            setBrandLoading(false)
        } else {
            setBrandLoading(false)
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    const fetchCelebrityData = async () => {
        const result = await celebrityListService({ token })
        if (result?.data?.success) {
            setCelebrityList(result?.data?.data)
            setCelebrityLoading(false)
        } else {
            setCelebrityLoading(false)
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    useEffect(() => {
        if (categoryList?.length === 0) {
            fetchProductCategoryData();
        }
        if(brandList?.length === 0){
            fetchBrandsData();
        }
        if(celebrityList?.length === 0){
            fetchCelebrityData();
        }
        const timeoutId = setTimeout(() => 3000);
        return function cleanup() {
            clearTimeout(timeoutId);
        }
    }, [])


    const handleGetProduct = async(e) => {
        let result = null;
        if(e){
            result = await singleCategoryProductService({ token, id: e, perPage: 8, page: 1 })
        }else{
            result = await allProductService({ token, perPage: 8, page: 1 })
        }
        
        if (result?.data?.success) {
            setProductList(result?.data?.data?.products?.data)
            setProductLoading(false)
        } else {
            setProductLoading(false)
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    const handleGetBrands = async(e) => {
        if(e){
            const result =  await singleBrandListService({ token, id: e })
            if (result?.data?.success) {
                setFilteredBrand(result?.data?.data?.brands)
                setFilteredBrandLoading(false)
            } else {
                setFilteredBrandLoading(false)
                Swal.fire({
                    title: 'Error!',
                    text: result?.data?.message || "Something went wrong",
                    icon: 'error',
                    confirmButtonText: 'Try Again',
                    confirmButtonColor: "#242c44"
                })
                if (result?.data?.message === "Invalid API Key") {
                    removeUserSession();
                    navigate('/login');
                }
            }
        }
    }

    useEffect(() => {
        if (categoryList?.length !== 0) {
            handleGetProduct();
        }
        const timeoutId = setTimeout(() => 3000);
        return function cleanup() {
            clearTimeout(timeoutId);
        }
    }, [categoryList])

    const handleNavigateToSingleCategory = () => {
        navigate("/single-category")
    }

    return (
        <div className="App">
            <Header siteLogo={logo} />
            <div className="store-banner">
                <img className="hero" src={Storehero} alt="" />
                <div className="store-banner-cont">
                    <Container>
                        <h2 className="store-banner-cont-title">Funds Raised for Jynie Foundation</h2>
                        <div className="store-banner-cont-donations">
                            <div className="store-banner-cont-donations-post">
                                <img className="" src={DonationimgFirst} alt="" />
                                <div className="store-banner-cont-donations-post-cont">
                                    <span className="store-banner-cont-donations-post-cont-price">2,500</span>
                                    <span className="store-banner-cont-donations-post-cont-text">Members donated</span>
                                </div>
                            </div>
                            <div className="store-banner-cont-donations-post">
                                <img className="" src={DonationimgSecond} alt="" />
                                <div className="store-banner-cont-donations-post-cont">
                                    <span className="store-banner-cont-donations-post-cont-price"><small>USD</small>25,500.75</span>
                                    <span className="store-banner-cont-donations-post-cont-text">Donations Collected</span>
                                </div>
                            </div>
                        </div>
                    </Container>
                </div>
            </div>
            <Container>
                {cateGoryloading ? <Loader/> : <Categorycarousel categoryList={categoryList} />}
                <div className="s-category pt-2 mb-5">
                    <Row className="apartTitle">
                        <Col md={{ span: 12, }} className="">{
                            <h2 className="title-with-dash">New Added Products</h2>
                        }</Col>
                    </Row>
                    <Row className="position-relative">
                        <Col md={{ span: 12, }} xs={{ span: 12, }}>
                            {cateGoryloading ? <Loader/> :
                            <Tabs onSelect={(e) => handleGetProduct(e)} defaultActiveKey="" id="product-category-list" className="productTabs">
                                <Tab eventKey="" title="All Product">
                                    {productLoading ? 
                                        <Loader/> : 
                                        <Row className="reset-col-padding-mobile">
                                            {productList?.map(productItem => 
                                                <Col key={productItem.id} md={{ span: 6, }} xs={{ span: 6, }} lg={{ span: 3, }}>
                                                   { productItem?.is_auction ? 
                                                        <ProductofBid product={productItem} /> :
                                                        <ProductofList product={productItem} /> 
                                                    }
                                                </Col>
                                            )}
                                        </Row>
                                    }
                                </Tab>
                                {categoryList?.map(item => 
                                    <Tab eventKey={item.id} title={item.name[0].toUpperCase()+item.name.substring(1)} key={item.id}>
                                      {productLoading ? 
                                        <Loader/> : 
                                        <Row className="reset-col-padding-mobile">
                                            {productList?.map(productItem => 
                                                <Col key={productItem.id} md={{ span: 6, }} xs={{ span: 6, }} lg={{ span: 3, }}>
                                                    { productItem?.is_auction ? 
                                                        <ProductofBid product={productItem} /> :
                                                        <ProductofList product={productItem} /> 
                                                    }
                                                </Col>
                                            )}
                                        </Row>
                                    }
                                  </Tab>  
                                )}
                            </Tabs>
                            }
                        </Col>
                    </Row>
                    <Row className="justified-content-center mt-4">
                        <Col md={{ span: 3, }} xs={{ span: 12, }} className="mx-auto">
                            <Button onClick={handleNavigateToSingleCategory} className="btn-outline withIcon">View all Products</Button>
                        </Col>
                    </Row>
                </div>

                <div className="s-category mb-5">
                    <Row className="apartTitle">
                        <Col md={{ span: 12, }} className="">{
                            <h2 className="title-with-dash">Shop By Brand</h2>
                        }</Col>
                    </Row>
                    <Row className="position-relative">
                        <Col md={{ span: 12, }} xs={{ span: 12, }}>
                            {brandLoading ? <Loader/> :
                            <Tabs onSelect={(e) => handleGetBrands(e)} defaultActiveKey="" id="brand-list" className="productTabs">
                                <Tab eventKey="" title="All Brand">
                                    <Row className="reset-col-padding-mobile">
                                        {brandList?.filter((_item, idx) => idx < 8)?.map(brandItem => 
                                            <Col key={brandItem.id} md={{ span: 6, }} xs={{ span: 6, }} lg={{ span: 3, }}>
                                                <Brand brandItem={brandItem}/>
                                            </Col>
                                        )}
                                    </Row>
                                </Tab>
                                {categoryList?.map(item => 
                                    <Tab eventKey={item.id} title={item.name[0].toUpperCase()+item.name.substring(1)} key={item.id}>
                                    {filteredBrandLoading ? <Loader /> : 
                                        <Row className="reset-col-padding-mobile">
                                            {filteredBrand?.filter((_item, idx) => idx < 8)?.map(brandItem => 
                                                <Col key={brandItem.id} md={{ span: 6, }} xs={{ span: 6, }} lg={{ span: 3, }}>
                                                    <Brand brandItem={brandItem} />
                                                </Col>
                                            )}
                                        </Row>
                                        }
                                    </Tab>  
                                )}
                            </Tabs>
                            }
                        </Col>
                    </Row>
                    <Row className="justified-content-center mt-4">
                        <Col md={{ span: 3, }} xs={{ span: 12, }} className="mx-auto">
                            <Button className="btn-outline withIcon">View all Brands</Button>
                        </Col>
                    </Row>
                </div>

                <div className="s-category-celebrity mb-5">
                    <Row className="apartTitle">
                        <Col md={{ span: 12, }} className="">{
                            <h2 className="title-with-dash">Popular Celebrities</h2>
                        }</Col>
                    </Row>

                    {celebrityLoading ? <Loader/> :
                        <Row className="mobile-slide">
                            {celebrityList?.filter((_item, idx) => idx < 8)?.map(celebrityItem =>
                                <Col key={celebrityItem?.id} md={{ span: 6, }} xs={{ span: 12, }} lg={{ span: 4, }}>
                                    <Celebrities celebrityItem={celebrityItem} />
                                </Col>
                            )} 
                        </Row>
                    }
                   
                </div>
            </Container>
            <Footer />
        </div>
    );
};

export default Storepage;
