import React, {} from "react";
import { Card} from "react-bootstrap";
import { Link } from "react-router-dom";
import Brandimage from './../../assets/images/brand-logo/gucci-brand-logo.png';
import './Brandmodule.scss';

const Brand = ({brandItem}) => {
    return (
        <div className="brand-apart">
            <Card className="brand-widget">
                <div className="brand-widget-image">
                    <Card.Img variant="top" src={Brandimage} />
                </div>
                <Card.Body className="brand-widget-body">
                    <Card.Title className="brand-widget-body-title"><Link to="">{brandItem.name}</Link></Card.Title>
                </Card.Body>
            </Card>
        </div>
    );
};

export default Brand;
