import React, { useState, useEffect } from "react";
import { Col, Container, Row, Button, ListGroup, Card, Tabs, Tab, Table } from 'react-bootstrap';
import { Swiper, SwiperSlide } from "swiper/react";
import { FreeMode, Navigation, Thumbs } from "swiper";

import { Link, useLocation, useNavigate } from 'react-router-dom'
import Swal from 'sweetalert2';


// Auth-Service
import { removeUserSession, getToken } from "../../utils/AuthService";

// API-Services
import {getSingleProductService, addToWishListService, addToCartService} from "../../service/product.service"

import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';
import Loader from "../../components/Loader/Loader";

import ProductImageSlider from '../../components/ProductImageSlider/ProductImageSlider';
import IDcounter from '../../components/IDcounter/IDcounter';

import logo from "../../assets/images/logo.png";
import Singleproductimg from '../../assets/images/feed/gucci-black-tshirt.jpeg';
import Singleproductimg2 from '../../assets/images/feed/oversize-drop-shirt.jpeg';

// Import Swiper styles
import "swiper/css";
import "swiper/css/free-mode";
import "swiper/css/navigation";
import "swiper/css/thumbs";

import './SingleproductModule.scss';


const url = process.env.REACT_APP_APP_API_BACKEND_SERVER_IMAGE

const Singleproduct = () => {

    const token = getToken();
    const navigate = useNavigate();
    const location = useLocation()
    const { productID } = location.state;

    const [loading, setLoading] = useState(true);
    const [productData, setProductData] = useState(null);
    const [inCart, setInCart] = useState(false);
    const [inWishList, setInWishList] = useState(false);
 

    const [thumbsSwiper, setThumbsSwiper] = useState(null);
    

    const handleGetSingleProduct = async () => {
        const result =  await getSingleProductService({ token, id: productID})
        if (result?.data?.success) {
            const value = result?.data?.data?.product
            setProductData(value);
            setInCart(value?.cart_item_product_id === value?.id);
            setInWishList(value?.wishlist_product_id === value?.id);
            setLoading(false)
        } else {
            setLoading(false)
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    useEffect(() => {
        if(productID){
            handleGetSingleProduct();
        }
        const timeoutId = setTimeout(() => 3000);
        return function cleanup() {
            clearTimeout(timeoutId);
        }
    }, [productID])


    const handleAddToCart = async(id) => {
        const  result = await addToCartService({ token, product_id: productData?.id, quantity: 2});
        if (result?.data?.success) {
            setInCart(true)
        } else {
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    const handleAddToWishList = async(id) => {
        const  result = await addToWishListService({ token, user_product_id: productData?.id});
        if (result?.data?.success) {
            setInWishList(true)
        } else {
            Swal.fire({
                title: 'Error!',
                text: result?.data?.message || "Something went wrong",
                icon: 'error',
                confirmButtonText: 'Try Again',
                confirmButtonColor: "#242c44"
            })
            if (result?.data?.message === "Invalid API Key") {
                removeUserSession();
                navigate('/login');
            }
        }
    }

    return (
        <div className="App">
            <Header siteLogo={logo} />
            <Container>
                {loading ? <Loader/> :
                <div className="single-product page-top">
                    <Row>
                        <Col xs={{ span: 12, }} md={{ span: 6, }} lg={{ span: 6, }}>
                            {/* <div className="productSwiper-wrap">
                                <Swiper
                                    onSwiper={setThumbsSwiper}
                                    spaceBetween={10}
                                    slidesPerView={4}
                                    freeMode={true}
                                    watchSlidesProgress={true}
                                    modules={[FreeMode, Navigation, Thumbs]}
                                    className="swiper-thumb"
                                >
                                    {productData?.image_urls?.map(thumbItem =>
                                        <SwiperSlide key={thumbItem}>
                                            <img src={`${url}${thumbItem}`} />
                                        </SwiperSlide>
                                    )}
                                </Swiper>
                                <div className="productSwiper-wrap-right">
                                    <Swiper
                                        style={{
                                            "--swiper-navigation-color": "#fff",
                                            "--swiper-pagination-color": "#fff",
                                        }}
                                        spaceBetween={10}
                                        navigation={true}
                                        //thumbs={{ swiper: thumbsSwiper }}
                                        modules={[FreeMode, Navigation, Thumbs]}
                                        className="swiper-slide2"
                                    >
                                        {productData?.image_urls?.map( thumbItem =>
                                            <SwiperSlide key={thumbItem}>
                                                <img src={`${url}${thumbItem}`} />
                                            </SwiperSlide>
                                        )}
                                    </Swiper>
                                </div>
                            </div> */}
                            <ProductImageSlider />
                        </Col>
                        <Col xs={{ span: 12, }} md={{ span: 6, }} lg={{ span: 6, }}>
                            <div className="s-product">
                                <h2 className="s-product-title">{productData?.name}</h2>
                                <Link to="/user-profile" state={{userID: productData?.user?.id}}>
                                    <span className="s-product-category">{productData?.user?.username}</span>
                                </Link>
                                <div className="s-product-price">
                                    <span className="s-product-price-offer">${productData?.price}.00</span>
                                    <span className="s-product-price-main">$550.00</span>
                                </div>
                                <p className="s-product-text">{productData?.description}</p>
                                {/* <div className="s-product-qty">
                                    <span className="s-product-qty-add">+</span>
                                    <span className="s-product-qty-digit">1</span>
                                    <span className="s-product-qty-del">-</span>
                                </div> */}
                                <IDcounter/>
                                <Row className="s-product-btn">
                                    <Col xs={{ span: 6, }} lg={{ span: 6, }}>
                                        
                                        {inCart ? 
                                        <Button className="btn-base">Go to Bag</Button>:
                                        <Button onClick={handleAddToCart} className="btn-base">Add to Bag</Button>}
                                    </Col>
                                    <Col xs={{ span: 6, }} lg={{ span: 5, }}>
                                        {inWishList ? 
                                        <Button className="btn-outline">Wishlisted</Button> :
                                        <Button onClick={handleAddToWishList} className="btn-outline">Add to Wishlist</Button>}
                                    </Col>
                                </Row>
                                <div className="s-product-meta">
                                    <ListGroup className="s-product-meta-group">
                                        <ListGroup.Item className="s-product-meta-group-item">
                                            <span className="s-product-meta-group-item-label">Available:</span>
                                            <span className="s-product-meta-group-item-text">In Stock</span>
                                        </ListGroup.Item>
                                        <ListGroup.Item className="s-product-meta-group-item">
                                            <span className="s-product-meta-group-item-label">Brand:</span>
                                            <span className="s-product-meta-group-item-text">Gucci</span>
                                        </ListGroup.Item>
                                        <ListGroup.Item className="s-product-meta-group-item">
                                            <span className="s-product-meta-group-item-label">Category:</span>
                                            <span className="s-product-meta-group-item-text">Women, Clothing</span>
                                        </ListGroup.Item>
                                    </ListGroup>
                                </div>
                            </div>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={{ span: 12, }} lg={{ span: 12, }}>
                            <div className="product-description">
                                <Tabs defaultActiveKey="description" id="product-tab" className="product-description-top">
                                    <Tab eventKey="description" title="Description" className="product-description-title">
                                        <div className="product-description-box">
                                            <Card className="product-description-box-card">
                                                <Card.Body className="product-description-box-card-body">
                                                    <Card.Title className="product-description-box-card-body-title">Product Information</Card.Title>
                                                    <Card.Text className="product-description-box-card-body-text">
                                                        Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet. Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod
                                                    </Card.Text>
                                                </Card.Body>
                                            </Card>
                                            <Card className="product-description-box-card">
                                                <Card.Body className="product-description-box-card-body">
                                                    <Card.Title className="product-description-box-card-body-title">Material used</Card.Title>
                                                    <Card.Text className="product-description-box-card-body-text">
                                                        Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet. Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod
                                                    </Card.Text>
                                                </Card.Body>
                                            </Card>
                                        </div>
                                    </Tab>
                                </Tabs>
                            </div>
                        </Col>
                    </Row>
                </div>
                }
            </Container>
            <Footer />
        </div>
    );
};

export default Singleproduct;
