import axios from "axios"

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER


/**
* @function newsFeedListService
* @param {token}
* @description This function is used to get all news feed list
*/

export const newsFeedListService = async({ token, perPage, page }) =>{
    try {
        const result = await axios.get(`${url}/newsfeed?perPage=${perPage}&paginate=1&page=${page}`,{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}

/**
* @function newsFeedListOfOTherUserService
* @param {token, id}
* @description This function is used to get all news feed list for toher user
*/

export const newsFeedListOfOTherUserService = async({ token, perPage, page, id }) =>{
    try {
        const result = await axios.get(`${url}/newsfeed?user_id=${id}&perPage=${perPage}&paginate=1&page=${page}`,{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}

/**
* @function likeUnlikeService
* @param {token}
* @description This function is used to like and unlike post
*/

export const likeUnlikeService = async({ token, id }) =>{
    try {
        const result = await axios.post(`${url}/product/${id}/post/like`,{},{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}

/**
* @function commentService
* @param {token}
* @description This function is used to like and unlike post
*/

export const commentService = async({ token, id, description }) =>{
    try {
        const result = await axios.post(`${url}/product/${id}/post/comment`,{
            description
        },{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}

/**
* @function commentsListService
* @param {token}
* @description This function is used to like and unlike post
*/

export const commentsListService = async({ token, id, offset }) =>{
    try {
        const result = await axios.get(`${url}/product/${id}/post/comment?limit=20&offset=${offset}`,{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}