import React, { useState } from 'react';
import { Button, Col, Form, Row, Tab, Tabs } from 'react-bootstrap';
import Swal from 'sweetalert2';
import { useNavigate } from 'react-router-dom';

import Signin from '../SignIn/SignIn';
import { signUpService } from '../../service/auth.service';

import logo from '../../assets/images/logo.png';
import Footer from '../../components/Footer/Footer';
import Header from '../../components/Header/Header';

// Utility Service
import { setUserSession } from '../../utils/AuthService';

import './SignUpmodule.scss';

const Signup = () => {
  const navigate = useNavigate();
  const [notValidated, setNotValidated] = useState(false);
  const [userNameNotValidated, setUserNameNotValidated] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [showPasswordTwo, setShowPasswordTwo] = useState(false);
  const [terms, setTerms] = useState(false);

  const [formData, setFormData] = useState({
    email: '',
    username: '',
    password: '',
    password_confirmation: '',
  });
  const [passwordNotMatch, setPasswordNotMatch] = useState(false);

  const handleEmail = (e) => {
    setFormData({
      ...formData,
      email: e.target.value,
    });
  };
  const handlePassword = (e) => {
    setFormData({
      ...formData,
      password: e.target.value,
    });
  };

  const handleConfirmPassword = (e) => {
    if (formData.password === e.target.value) {
      setFormData({
        ...formData,
        password_confirmation: e.target.value,
      });
      setPasswordNotMatch(false);
    } else {
      setPasswordNotMatch(true);
    }
  };

  const handleUserName = (e) => {
    const userName = e.target.value;
    if(/^[a-zA-Z]{4,16}$/.test(userName)){
      setUserNameNotValidated(false)
      setFormData({
        ...formData,
        username: userName
      });
    }else{
      setUserNameNotValidated(true)
    }

  };

  const handleShowPasswordOne = () => {
    setShowPassword(!showPassword);
  };

  const handleShowPasswordTwo = () => {
    setShowPasswordTwo(!showPasswordTwo);
  };

  const handleTerms = (e) => {
    setTerms(e.target.checked);
  };

  const handleRemoveErrorMessage = () => {
    setNotValidated(false);
    setUserNameNotValidated(false);
  }

  /**
   * @function handleSubmit
   * @param event
   * @description This function is used to signup the user
   */

  const handleSubmit = async (event) => {
    event.preventDefault();
    const form = event.currentTarget;
    
    if (form.checkValidity() === false || userNameNotValidated) {
      setUserNameNotValidated(false);
      setNotValidated(true);
      return;
    }

    const result = await signUpService({
      username: formData.username,
      email: formData.email,
      password: formData.password,
      password_confirmation: formData.password_confirmation,
    });

    if (result?.data?.success) {
      setUserSession(result?.data?.data?.api_key, result?.data?.data?.user);
      navigate('/packages');
    } else {
      Swal.fire({
        title: 'Error!',
        text: result?.response?.data?.message || 'Something went wrong',
        icon: 'error',
        confirmButtonText: 'Try Again',
        confirmButtonColor: '#242c44',
      });
    }
  };

  return (
    <div className="App">
      <Header siteLogo={logo} />
      <div className="container mb-5 pb-5">
        <Row className="mb-5 main-top-padding">
          <Col md={{ span: 5 }} className="text-center mx-auto">
            {<h2 className="pageTitle">Account</h2>}
          </Col>
        </Row>
        <Row>
          <Col md={{ span: 8 }} lg={{ span: 5 }} className="mx-auto">
            {
              <Tabs
                defaultActiveKey="signin"
                id="signin-signup-tab"
                className="tabPanel">
                <Tab eventKey="signin" title="Sign In">
                  <Signin />
                </Tab>
                <Tab eventKey="signup" title="Sign Up">
                  <Form noValidate onSubmit={handleSubmit}>
                    <Form.Group className="mb-3 field-group" controlId="">
                      <Form.Label>Email address</Form.Label>
                      <Form.Control
                        className={notValidated ? 'error' : ''}
                        type="email"
                        placeholder="Enter email"
                        onBlur={handleEmail}
                        onChange={handleRemoveErrorMessage}
                        required
                      />
                      {notValidated && (
                        <span className="errorText show">
                          Enter valid email address
                        </span>
                      )}
                    </Form.Group>

                    <Form.Group className="mb-3 field-group" controlId="">
                      <Form.Label>Username</Form.Label>
                      <Form.Control
                        className={notValidated ? 'error' : ''}
                        placeholder="Enter User Name"
                        onBlur={handleUserName} 
                        onChange={handleRemoveErrorMessage}
                        required
                      />
                      {notValidated && (
                        <span className="errorText show">
                          Enter Valid Username
                        </span>
                      )}
                      {userNameNotValidated && (
                        <span className="errorText show">
                          Username should be only alphabets, unique and minimum 4 characters and maximum 16 characters
                        </span>
                      )}
                    </Form.Group>

                    <Form.Group className="mb-3 field-group" controlId="">
                      <Form.Label>Password</Form.Label>
                      <div className="password">
                        <Form.Control
                          className={notValidated ? 'error' : ''}
                          type={showPassword ? 'text' : 'password'}
                          placeholder="Password"
                          onBlur={handlePassword} 
                          onChange={handleRemoveErrorMessage}
                          required
                        />
                        <span
                          onClick={handleShowPasswordOne}
                          className={
                            showPassword
                              ? 'password-view show'
                              : 'password-view'
                          }></span>
                      </div>
                      {notValidated && (
                        <span className="errorText show">
                          Must be 8 or more characters
                        </span>
                      )}
                    </Form.Group>

                    <Form.Group className="mb-3 field-group" controlId="">
                      <Form.Label>Confirm Password</Form.Label>
                      <div className="password">
                        <Form.Control
                          className={notValidated ? 'error' : ''}
                          type={showPasswordTwo ? 'text' : 'password'}
                          placeholder="Password"
                          onBlur={handleConfirmPassword}
                          required
                        />
                        <span
                          onClick={handleShowPasswordTwo}
                          className={
                            showPasswordTwo
                              ? 'password-view show'
                              : 'password-view'
                          }></span>
                      </div>
                      {passwordNotMatch && (
                        <span className="errorText show">
                          Please make sure your passwords match.
                        </span>
                      )}
                    </Form.Group>

                    <Row className="my-4">
                      <Col>
                        <Form.Group
                          className="mb-1 mt-2"
                          controlId="formBasicCheckbox">
                          <Form.Check
                            onChange={handleTerms}
                            type="checkbox"
                            label="I Accept all the Privacy Policy & Terms and Condition."
                          />
                        </Form.Group>
                      </Col>
                    </Row>
                    <Button
                      variant="primary"
                      className={terms ? 'btn-base' : 'btn-base disabled'}
                      type="submit">
                      Create Account
                    </Button>
                  </Form>
                </Tab>
              </Tabs>
            }
          </Col>
        </Row>
      </div>
      <Footer />
    </div>
  );
};

export default Signup;
