import React, {} from "react";
import {Card} from "react-bootstrap";
import { useNavigate } from 'react-router-dom';

import Categoryimage from './../../assets/images/category-clothing.png';
import Categoryibagsmage from './../../assets/images/category-bags.png';
import Categoryishoesmage from './../../assets/images/category-shoes.png';

import { Navigation } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';

import './Categorycarouselmodule.scss';

const Categorycarousel = ({categoryList}) => {
    const navigate = useNavigate();
    const handleSingleCategory = (id) => {
        navigate('/single-category',{
            state:{
                id
            }
        });
    }

    return (
        <div className="category-carousel-wrap">
            <Swiper
                breakpoints={{
                    576: {
                    // width: 576,
                    slidesPerView: 2,
                    },
                    768: {
                    // width: 768,
                    slidesPerView: 2,
                    },
                    1024: {
                        // width: 768,
                        slidesPerView: 3,
                        },
                }}
                modules={[Navigation]}
                spaceBetween={50}
                slidesPerView={1}
                navigation
                >
                {
                    categoryList?.map((item) =>
                        <SwiperSlide key={item.id}>
                            <div className="category-carousel">
                                <div className="category-image">
                                    <Card.Img variant="top" src={Categoryimage} />
                                </div>
                                <div onClick={() => handleSingleCategory(item.id)} className="category-title">
                                    {item.name}
                                </div>
                            </div>
                        </SwiperSlide>
                    )
                }
            </Swiper>
        </div>
    );
};



export default Categorycarousel;
