import axios from "axios"

const url = process.env.REACT_APP_APP_API_BACKEND_SERVER


/**
* @function searchService
* @param {token, term}
* @description This function is used to search and get search result 
*/

export const searchService = async({ token, term }) =>{
    try {
        const result = await axios.get(`${url}/search?keyword=${term}`,{
            headers:{
                auth: token
            }
        })
        return result;
    }catch(err){
        return err
    } 
}